import { PuntoPagoService } from '../../../services/punto-pago.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { Mensaje } from 'src/app/config/constantes';
import { CuentacatastralService } from 'src/app/services/cuentacatastral.service';
import { TokenStorageService } from 'src/app/services/token-storage.service';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
declare var $: any;
@Component({
  selector: 'app-puntos-pago',
  templateUrl: './puntos-pago.component.html',
  styleUrls: ['./puntos-pago.component.scss']
})
export class PuntosPagoComponent implements OnInit {
  totalRecords =0;
  entidad:any = [];
  municipios: any = [];
  dataRelacion: any = {};
  page: number = 1;
  size: number = 10;

   constructor(private service: CuentacatastralService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService, private notifier: NotifierService, private authService: TokenStorageService,
    private formBuilder: FormBuilder, private modalService: NgbModal, private servicePunto: PuntoPagoService) {
  }

  ngOnInit(): void {
    this.getMunicipios();
    this.getPuntosDePagos();
  }

  getMunicipios() {
    this.ngxService.start();
    this.service.getMunicipios().subscribe(
      (response: any) => {
          this.ngxService.stop();
          this.municipios = response;
      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }

  getPuntosDePagos() {
    const params = {
      cantidad: 10,
      pagina: this.page,
      sortBy:"idPuntoPago",
      sortOrder: "asc",
      //filtros: JSON.stringify(deleteEmptyData())
    }
    this.ngxService.start();
    this.servicePunto.getAll(params).subscribe(
      (response: any) => {
        if(response){
          this.ngxService.stop();
          this.totalRecords = response.total;
          this.entidad = response.lista;
        }else {
          this.totalRecords = 0;
          this.entidad = [];
        }

      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }
  filtrar(){

  }
  limpiar(){

  }

  agregar(){
    this.router.navigate(['/gestion-interna/punto-de-pago/agregar']);
  }
  ver(id:any){
    this.router.navigate(['/gestion-interna/punto-de-pago/visualizar/'+id]);
  }
  editar(id:any){
    this.router.navigate(['/gestion-interna/punto-de-pago/editar/'+id]);
  }
  eliminarPuntoPago(item:any) {
    this.ngxService.start();
    if (item.idPuntoPago != null) {
      let body ={
        nombre: item.nombre,
        direccion: item.direccion,
        telefono: item.telefono,
        latitud: item.latitud,
        longitud: item.longitud,
        municipio: item.municipio.idMunicipio
      }
      this.servicePunto.delete(item.idPuntoPago, body).subscribe(
        (result: any) => {
          this.ngxService.stop();
          this.entidad = [];
          this.totalRecords = 0;
          this.notifier.notify('success', result.mensaje);
          this.getPuntosDePagos();

        },
        (error: any) => {
          this.ngxService.stop();
          this.notifier.notify('error', error.error.mensaje);
        }

      )
    } else {
      this.messageService.add({ severity: 'warning', summary: '¡Advertencia!', detail: 'Seleccione un registro' });
    }

  }
  eliminar(item:any) {
    this.confirmationService.confirm({
      message: '¿Está seguro que desea eliminar el punto de pago '+ item.nombre + '?',
      header: 'Confirmar Eliminación',
      icon: 'pi pi-info-circle',
      accept: () => {
        this.eliminarPuntoPago(item);
      },
      reject: () => {

      }
    });
  }
  paginate(event: any) {
    this.page = event.page +1;
    this.entidad = [];
    this.getPuntosDePagos();
  }

}

