import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NotifierService } from 'angular-notifier';
import { DeviceDetectorService } from 'ngx-device-detector';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { deleteEmptyData } from 'src/app/config/app-utils';
import { Mensaje } from 'src/app/config/constantes';
import { CuentacatastralService } from 'src/app/services/cuentacatastral.service';
import { DataSharingService } from 'src/app/services/data-sharing.service';
import { TokenStorageService } from 'src/app/services/token-storage.service';
import { UsuariosService } from 'src/app/services/usuarios.service';
import { CuentaCatastralAgregarComponent } from '../cuenta-catastral-agregar/cuenta-catastral-agregar.component';
import { CuentaCatastralVerMapaComponent } from '../cuenta-catastral-ver-mapa/cuenta-catastral-ver-mapa.component';

@Component({
  selector: 'app-cuenta-catastral-facturacion',
  templateUrl: './cuenta-catastral-facturacion.component.html',
  styleUrls: ['./cuenta-catastral-facturacion.component.scss']
})
export class CuentaCatastralFacturacionComponent implements OnInit {

  municipios = [];
  cuentas: any =[];
  itemsPorPagina = 10;
  length =0;
  pageSize = 10;
  pagina = 1;
  pageIndex = 0;
  data: any = {};
  usuarios = [];
  facturas: any =[];
  usuario: any;

  verMas = 'Mostrar las últimas facturas';
  verMasIcon = 'pi pi-arrow-down';
  booleanVerMas = false;
  activatedEdit = true;
  submit = false;
  id: any;
  primerLogueo = false;
  @Output() closeResult: EventEmitter<boolean> = new EventEmitter();
  result: any;
  isMobile = false;

   constructor(private service: CuentacatastralService, private serviceUsuario: UsuariosService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService, private notifier: NotifierService,
    private formBuilder: FormBuilder, private authService: TokenStorageService, private deviceService: DeviceDetectorService,
    private modalService: NgbModal, private dataSharingService: DataSharingService) {
      /* pregunta si es su primer logueo */
      
      this.primerLogueo = this.authService.getPrimerLogin();
      this.id = this.authService.getUserId();
      this.isMobile = this.deviceService.isMobile();
     
     
  }

  ngOnInit(): void {
    if(this.primerLogueo){
      this.agregarCuentaCatastral();
    }
    this.getMunicipios();
    this.getCuentas();
  
  }
  paginar() {
    this.length = 0;
    this.itemsPorPagina = 10;
    this.pageSize = 10;
    //this.getUsuarios();
  }
  callPagination($event: any) {
    this.data = {
      totalItem: $event.pageSize,
      pageIndex: $event.pageIndex + 1
    };
    this.pagina = this.data.pageIndex;
    this.paginar();
  }
  getMunicipios() {
    this.ngxService.start();
    this.service.getMunicipios().subscribe(
      (response: any) => {
          this.ngxService.stop();
          this.municipios = response;
      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }
  getUser() {
    this.ngxService.start();
    this.serviceUsuario.getById(this.id).subscribe(
      (response: any) => {
          this.ngxService.stop();
          this.usuario = response;
         
      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }
  getCuentas() {
    this.ngxService.start();
    this.service.getCuentas(this.id).subscribe(
      (response: any) => {
         if(response.exitoso){
          this.ngxService.stop();
          this.cuentas = response.data;
         }else{
          this.ngxService.stop();
          this.notifier.notify('error', response.mensaje);
          this.cuentas = response.data;
         }
      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }


  agregarCuentaCatastral(){
    const modalRef : any = this.modalService
    .open(CuentaCatastralAgregarComponent,{size:'xl',backdrop:'static'});
    modalRef.componentInstance.titulo='Agregar';
    modalRef.componentInstance.agregar=true;
    modalRef.componentInstance.id=this.id;
    modalRef.result.then((result:any) => {
      this.getCuentas();
    }, (reason: any) => {
      
    });
  }


  editarCuentaCatastral(id: any){
    //console.log('se presiono el boton editar')
    //console.log('se va editar la cuenta con id: ' + id);
    const modalRef : any = this.modalService
    .open(CuentaCatastralAgregarComponent,{size:'xl',backdrop:'static'});
    modalRef.componentInstance.titulo='Editar';
    modalRef.componentInstance.idCuenta=id;
    modalRef.componentInstance.id=this.id;
    modalRef.componentInstance.editar=true;
    modalRef.result.then((result:any) => {
      this.getCuentas();
    }, (reason: any) => {
      
    });
  }
  eliminarCuenta(id:any) {
    this.ngxService.start();
    if (id != null) {
      this.service.desactivarCuenta(id).subscribe(
        (result: any) => {
          this.ngxService.stop();
          this.notifier.notify('success', result.mensaje);
          this.getCuentas();

        },
        (error: any) => {
          this.ngxService.stop();
          this.notifier.notify('error', error.error.mensaje);
        }

      )
    } else {
      this.messageService.add({ severity: 'warning', summary: '¡Advertencia!', detail: 'Seleccione un registro' });
    }

  }
  eliminar(id:any) {
    this.confirmationService.confirm({
      message: '¿Está seguro que desea eliminar la cuenta?',
      header: 'Confirmar Eliminación',
      icon: 'pi pi-info-circle',
      accept: () => {
        this.eliminarCuenta(id);
      },
      reject: () => {

      }
    });
  }

  descargar(item:any){
    this.ngxService.start();
    this.service.descargarFactura(item.idFactura).subscribe(
      (response: any) => {
          this.ngxService.stop();
          
          if(response.data != null){
            this.decodeBase64(response.data,  item.facturaNumero+'.pdf');
          } else {
            this.notifier.notify('error', Mensaje.ERROR_FACTURA);
          }
      },
      (response: any) => {
        this.ngxService.stop();
        this.notifier.notify('error', Mensaje.ERROR_FACTURA);
       
      }

    )
  }
  decodeBase64(file: any, nombre:any ) {
    const blobData = this.convertBase64ToBlobData(file);
    if (window.navigator && window.navigator.msSaveOrOpenBlob) { // IE
      window.navigator.msSaveOrOpenBlob(blobData, nombre);
    } else { // chrome
      const blob = new Blob([blobData], { type: 'application/octet-stream' });
      const url = window.URL.createObjectURL(blob);
      
      const link = document.createElement('a');
      link.href = url;
      link.download = nombre;
      link.click();
      //window.open(url,'_blank');
     // window.open(url);


    

    }
  }
  convertBase64ToBlobData(base64Data: string, contentType: string= 'application/octet-stream', sliceSize= 512) {
    const byteCharacters = atob(base64Data);
    const byteArrays = [];

    for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
      const slice = byteCharacters.slice(offset, offset + sliceSize);

      const byteNumbers = new Array(slice.length);
      for (let i = 0; i < slice.length; i++) {
        byteNumbers[i] = slice.charCodeAt(i);
      }

      const byteArray = new Uint8Array(byteNumbers);

      byteArrays.push(byteArray);
    }

    const blob = new Blob(byteArrays, { type: contentType });
    return blob;
  }
  abrirMas(item: any){
    console.log(item);
  
    this.booleanVerMas = !this.booleanVerMas;
    if(this.booleanVerMas){
      this.getFacturas(item.identificadorCuentaCatastral, item.numeroCuentaCatastral);
      this.verMas = 'Ocultar las últimas facturas';
      this.verMasIcon = 'pi pi-arrow-up';
    } else {
      this.facturas = [];
      this.verMas = 'Mostrar las últimas facturas';
      this.verMasIcon = 'pi pi-arrow-down';

    }
  }

  verMapa(latitud: any, longitud: any){
    const modalRef : any = this.modalService
    .open(CuentaCatastralVerMapaComponent,{size:'xl',backdrop:'static'});
    modalRef.componentInstance.latitud=latitud;
    modalRef.componentInstance.longitud=longitud;
  }

  getFacturas(identificadorCuentaCatastral: any, numeroCuentaCatastral:any){
    let filtros={"identificadorCuentaCatastral":identificadorCuentaCatastral,"numeroCuentaCatastral":numeroCuentaCatastral}
     const params = {
      cantidad: 5,
      pagina: 1,
      sortBy:"idFactura",
      sortOrder: "desc",
      filtros:JSON.stringify(deleteEmptyData(filtros))
    }
    this.ngxService.start();
    this.service.getListaFacturas(params).subscribe(
      (response: any) => {
        if(response){
          this.ngxService.stop();
          this.facturas= response.data.lista;
        }else {
          this.facturas=[];
        }

      },
      (response: any) => {
        this.ngxService.stop();
        
      })
  }


}
