import { Component, OnInit } from '@angular/core';
import { DeviceDetectorService } from 'ngx-device-detector';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { DiasRecoleccionService } from 'src/app/services/dias-recoleccion.service';
declare var $: any;
declare const google: any;
@Component({
  selector: 'app-contacto',
  templateUrl: './contacto.component.html',
  styleUrls: ['./contacto.component.scss']
})
export class ContactoComponent implements OnInit {
  overlays: any;
  options: any;
  entidad: any =[];
  constructor(  private ngxService: NgxUiLoaderService,private recoleccionService: DiasRecoleccionService,
     private deviceService: DeviceDetectorService) {this.isMobile = this.deviceService.isMobile(); }
  isMobile = false;
  ngOnInit(): void {
    this.options = {
      center: { lat:  -25.313192494788872, lng:-57.55362932047217 },
      zoom: 19
    };
    this.overlays = [
      new google.maps.Marker({
        position: { lat:  -25.313192494788872, lng:-57.55362932047217 }, title: 'Ecologia Verde S.A. Recolector De Basuras',
        icon: {
          url: 'assets/img/icono-mapa.png',
          scaledSize: new google.maps.Size(35, 50),
          origin: new google.maps.Point(0, 0),
          labelOrigin: new google.maps.Point(23, 19)
        },
        draggable: false
      }),
    ];
    this.obtenerDatos();
  }

  obtenerDatos(){
    this.ngxService.start();
    this.recoleccionService.getContactos().subscribe(
      (response: any) => {
          this.ngxService.stop();
          this.entidad = response;
      },
      (response: any) => {
        this.ngxService.stop();
      }

    )
  }
  ubicarMarker(){
    this.options = {
      center: { lat: this.entidad.latitud, lng: this.entidad.longitud },
      zoom: 10
    };
    this.overlays = [
      new google.maps.Marker({
        position: { lat: this.entidad.latitud, lng: this.entidad.longitud,   zoom: 10 }, title: 'Ubicación Ecología Verde',
        icon: {
          url: 'assets/img/icono-mapa.png',
          scaledSize: new google.maps.Size(35, 50),
          origin: new google.maps.Point(0, 0),
          labelOrigin: new google.maps.Point(23, 19)
        },
        draggable: false
      }),
    ];
  }


}
