import { AuthService } from './../../services/auth.service';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { Mensaje } from 'src/app/config/constantes';

@Component({
  selector: 'app-confirmar-email',
  templateUrl: './confirmar-email.component.html',
  styleUrls: ['./confirmar-email.component.scss']
})
export class ConfirmarEmailComponent implements OnInit {

  hide = true;
  form: FormGroup;
  submit = false;
  isLoggedIn = false;
  isLoginFailed = false;
  errorMessage = '';
  get f() {
    return this.form.controls;
  }
  constructor(public router: Router, private formBuilder: FormBuilder, private ngxService: NgxUiLoaderService,
    private notifierService: NotifierService, private service: AuthService) {
    this.form = this.formBuilder.group(
      {
        correo: [null],
      }
    );
  }

  ngOnInit(): void {
  }
  enviar(){
    this.ngxService.start();
    this.submit = true;
    if (this.form.invalid) {
      this.ngxService.stop();
      this.notifierService.notify('error', Mensaje.VERIFICAR_CAMPOS);
      return;
    }
    let body = {
      correo: this.form.controls.correo.value,
    }
    this.service.enviarCorreo(body).subscribe(
      (response:any) => {
        if(response){
         this.ngxService.stop();
         if(!response.exitoso){
          this.notifierService.notify('error', response.mensaje);
         }else{
          this.notifierService.notify('success', response.mensaje);
         }
        }
      },
      (response:any) => {
        this.ngxService.stop();
      }
    );
  }


}
