import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { deleteEmptyData } from 'src/app/config/app-utils';
import { CuentacatastralService } from 'src/app/services/cuentacatastral.service';
import { ReclamosService } from 'src/app/services/reclamos.service';
import { TokenStorageService } from 'src/app/services/token-storage.service';

@Component({
  selector: 'app-reclamos',
  templateUrl: './reclamos.component.html',
  styleUrls: ['./reclamos.component.scss']
})
export class ReclamosComponent implements OnInit {
  es: any = [];
  municipios = [];
  data: any = {};
  usuarios = [];
  form: FormGroup;
  totalRecords =0;
  entidad:any = [];
  dataRelacion: any = {};
  page: number = 1;
  size: number = 10;
  filtros = {
    fechaDesde: null,
    fechaHasta:null,
    nombreCliente:null,
    apellidoCliente:null,
    estadoReclamo:null 
  }
  estados = [{id:'PEN', value:'PENDIENTE'},{id:'RES', value:'RESPONDIDO'},{id:'RES', value:'RESUELTO'}]
   constructor(private service: ReclamosService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService, private notifier: NotifierService, private authService: TokenStorageService,
    private formBuilder: FormBuilder, private modalService: NgbModal) {

      this.form = this.formBuilder.group(
        {
          desde: [null],
          hasta: [null],
          cliente: [null],
          estado: [null]
        }
      );
  }

  ngOnInit(): void {
    this.getEntidad();
    this.inicializarDatePicker();
  }
  inicializarDatePicker(){
    this.es = {
      firstDayOfWeek: 1,
      dayNames: [ "domingo","lunes","martes","miércoles","jueves","viernes","sábado" ],
      dayNamesShort: [ "dom","lun","mar","mié","jue","vie","sáb" ],
      dayNamesMin: [ "D","L","M","M","J","V","S" ],
      monthNames: [ "enero","febrero","marzo","abril","mayo","junio","julio","agosto","septiembre","octubre","noviembre","diciembre" ],
      monthNamesShort: [ "ene","feb","mar","abr","may","jun","jul","ago","sep","oct","nov","dic" ],
      today: 'Hoy',
      clear: 'Borrar'
    }
  }

  getEntidad() {
    const params = {
      cantidad: 10,
      pagina: this.page,
      sortBy:"idReclamo",
      sortOrder: "desc",
      filtros: JSON.stringify(deleteEmptyData(this.filtros))
    }
    this.ngxService.start();
    this.service.get(params).subscribe(
      (response: any) => {
        this.ngxService.stop();
        if(!response.exitoso){
          this.notifier.notify('error', response.mensaje);
          this.totalRecords = 0;
          this.entidad = [];
        }else {
          this.totalRecords = response.data.total;
          this.entidad = response.data.lista;
        }

      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }

  parseFecha(valor:any) {
    if(valor === null || valor === ''){
      return valor;
    }else {
      var event = new Date(valor);
      let fecha = JSON.stringify(event);
      fecha = fecha.slice(1,11);
      var fechaList = fecha.split('-');
      return fechaList[2] + '/' + fechaList[1] + '/' +fechaList[0];
    }
  }
  filtrar(){
    this.filtros = {
      fechaDesde: this.parseFecha(this.form.controls.desde.value),
      fechaHasta:this.parseFecha(this.form.controls.hasta.value),
      nombreCliente:this.form.controls.cliente.value ,
      apellidoCliente:this.form.controls.cliente.value ,
      estadoReclamo:this.form.controls.estado.value 
    }
    this.getEntidad();
  }
  limpiar(){
    this.filtros = {
      fechaDesde: null,
      fechaHasta:null,
      nombreCliente:null,
      apellidoCliente:null,
      estadoReclamo:null 
    }
    this.form.reset();
    this.page = 1;
    this.getEntidad();
  }
  ver(id:any){
    this.router.navigate(['/gestion-interna/reclamo/visualizar/'+id]);
  }
  atender(id:any){
    this.router.navigate(['/gestion-interna/reclamo/atender/'+id]);
  }
  eliminarReclamo(item:any) {
    this.ngxService.start();
    if (item != null) {
      this.service.eliminar(item).subscribe(
        (result: any) => {
          this.ngxService.stop();
          if(result.exitoso){
            this.entidad = [];
            this.totalRecords = 0;
            this.notifier.notify('success', result.mensaje);
            this.getEntidad();
          } else {
            this.notifier.notify('error', result.mensaje);
          }
         

        },
        (error: any) => {
          this.ngxService.stop();
          this.notifier.notify('error', error.mensaje);
        }

      )
    } else {
      this.messageService.add({ severity: 'warning', summary: '¡Advertencia!', detail: 'Seleccione un registro' });
    }

  }
  eliminar(item:any) {
    this.confirmationService.confirm({
      message: '¿Está seguro que desea eliminar el reclamo ?',
      header: 'Confirmar Eliminación',
      icon: 'pi pi-info-circle',
      accept: () => {
        this.eliminarReclamo(item);
      },
      reject: () => {

      }
    });
  }
  paginate(event: any) {
    this.page = event.page +1;
    this.entidad = [];
    this.getEntidad();
  }

}
