import { BannerService } from './../../services/banner.service';
import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { CuentacatastralService } from 'src/app/services/cuentacatastral.service';
import { DiasRecoleccionService } from 'src/app/services/dias-recoleccion.service';
import { DomSanitizer } from '@angular/platform-browser';
import { DeviceDetectorService } from 'ngx-device-detector';
import { NgbCarousel, NgbSlideEvent, NgbSlideEventSource } from '@ng-bootstrap/ng-bootstrap';
import { ViewChild } from '@angular/core';

@Component({
  selector: 'app-inicio',
  templateUrl: './inicio.component.html',
  styleUrls: ['./inicio.component.scss']
})
export class InicioComponent implements OnInit {
  banners: any = [];
  bannersMobile: any = [];
  isMobile = false;
  constructor(private service: BannerService, private deviceService: DeviceDetectorService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService, private notifier: NotifierService,private sanitizer: DomSanitizer,
    private formBuilder: FormBuilder, private route: ActivatedRoute, private cuentaService: CuentacatastralService) {
      this.isMobile = this.deviceService.isMobile();
    }
    images = [62, 83, 466, 965, 982, 1043, 738].map((n) => `https://picsum.photos/id/${n}/900/500`);

    paused = false;
    unpauseOnArrow = false;
    pauseOnIndicator = false;
    pauseOnHover = false;
    pauseOnFocus = false;

    @ViewChild('carousel', { static: true })
  carousel!: NgbCarousel;

    togglePaused() {
      if (this.paused) {
        this.carousel.cycle();
      } else {
        this.carousel.pause();
      }
      this.paused = !this.paused;
    }

    onSlide(slideEvent: NgbSlideEvent) {
      if (this.unpauseOnArrow && slideEvent.paused &&
        (slideEvent.source === NgbSlideEventSource.ARROW_LEFT || slideEvent.source === NgbSlideEventSource.ARROW_RIGHT)) {
        this.togglePaused();
      }
      if (this.pauseOnIndicator && !slideEvent.paused && slideEvent.source === NgbSlideEventSource.INDICATOR) {
        this.togglePaused();
      }
    }
  ngOnInit(): void {
    this.getBanners();
  }

  getBanners(){
    this.ngxService.start();
    this.service.getAll().subscribe(
      (response: any) => {
          this.ngxService.stop();
          console.log(response);
          for (const iterator of response) {
            this.deserializarImagen(iterator);
          }
      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }
  deserializarImagen(data: any) {
    var aux = {};
    var auxMobile = {};
    const dato = 'data:image/' + data.tipoArchivo +';base64,' +
    (this.sanitizer.bypassSecurityTrustResourceUrl(data.imagen) as any).changingThisBreaksApplicationSecurity;
    const datoMobile = 'data:image/' + data.tipoArchivo +';base64,' +
    (this.sanitizer.bypassSecurityTrustResourceUrl(data.imagenMobil) as any).changingThisBreaksApplicationSecurity;
      aux = {
        title: data.titulo,
        summery: data.titulo,
        url: dato
      }
      auxMobile = {
        title: data.titulo,
        summery: data.titulo,
        url: datoMobile
      }
      this.banners.push(aux);
      this.bannersMobile.push(auxMobile);
      if(data.url != null && data.url != ''){
        aux = {
          title: data.titulo,
          summery: data.titulo,
          url: data.url
        }
        auxMobile = {
          title: data.titulo,
          summery: data.titulo,
          url: data.url
        }
        this.banners.push(aux);
        this.bannersMobile.push(auxMobile);
      }
    
  }
}

