import { ViewChild } from '@angular/core';
import { ElementRef } from '@angular/core';
import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { Mensaje } from 'src/app/config/constantes';
import { BannerService } from 'src/app/services/banner.service';
import { CuentacatastralService } from 'src/app/services/cuentacatastral.service';
import { UsuariosService } from 'src/app/services/usuarios.service';

@Component({
  selector: 'app-banner-form',
  templateUrl: './banner-form.component.html',
  styleUrls: ['./banner-form.component.scss']
})
export class BannerFormComponent implements OnInit {
  @Input() agregar: any;
  @Input() id: any;
  @Input() banner: any =[];
  @ViewChild('fileInput')
  fileInput!: ElementRef;
  fileInputMovil!: ElementRef;
  titulo = 'Agregar';
  fileName:any = null;
  fileType : any = null;
  fileNameMovil = null;
  fileTypeMovil : any = null;
  form: FormGroup;
  submit = false;
  base64 : any =  null;
  base64Movil: any =  null;
  get f() {
    return this.form.controls;
  }
  constructor(private modalService: NgbModal, public activeModal: NgbActiveModal,private service: BannerService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService, private notifier: NotifierService,
    private formBuilder: FormBuilder, private route: ActivatedRoute, private serviceCuenta: CuentacatastralService) {
      this.form = this.formBuilder.group(
        {
          titulo: [{value: null, disabled: false}, Validators.required],
          url: [{value: null, disabled: false}],
        }
      );
     }


  ngOnInit(): void {
    if(this.agregar){
      this.titulo = 'Agregar';
    } else {
      this.titulo = 'Editar';
      this.cargarForm();
    }
  }
  subirImagen(e: any, tipo: any){
    let files = e.files;
    if(e.files.length > 0){

      if(tipo === 'w'){
        this.fileName = files[0].name;
        this.fileType =  files[0].type.split('/');
        this.fileType = this.fileType[1];
        this.convertirBase64(files[0]);
      } else if(tipo === 'm'){
        this.fileNameMovil = files[0].name;
        this.fileTypeMovil =  files[0].type.split('/');
        this.fileTypeMovil = this.fileType[1];
        this.convertirBase64Movil(files[0]);
      }

    } else if (files[0].name === '' || files[0].name === null || files[0].name === undefined){
      this.fileInput.nativeElement.value = '';
      this.fileInputMovil.nativeElement.value = '';
    }
  }

  convertirBase64(archivo: any) {
    let reader = new FileReader();
    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsBinaryString(archivo);
  }
  convertirBase64Movil(archivo: any) {
    let reader = new FileReader();
    reader.onload = this._handleReaderLoadedMobil.bind(this);
    reader.readAsBinaryString(archivo);
  }
  _handleReaderLoaded(readerEvt : any) {
    const binaryString = readerEvt.target.result;
      this.base64 = btoa(binaryString);
  }
  _handleReaderLoadedMobil(readerEvt : any) {
    const binaryString = readerEvt.target.result;
      this.base64Movil = btoa(binaryString);
  }
  guardar(){

    this.submit = true;
  if (this.form.invalid) {
      this.notifier.notify('error', Mensaje.VERIFICAR_CAMPOS);
      return;
    } else{
      this.ngxService.start();
      let body = {
        titulo: this.form.controls.titulo.value,
        url: this.form.controls.url.value,
        imagen: this.base64,
        imagenMobil: this.base64Movil,
        tipoArchivo: this.fileType,
      }
      if(this.agregar){
        this.service.agregar(JSON.stringify(body)).subscribe(
          (response: any) => {
            if(!response.exitoso){
              this.ngxService.stop();
              this.notifier.notify('error', response.mensaje);
              } else {
              this.ngxService.stop();
              this.notifier.notify('success', response.mensaje);
              this.activeModal.dismiss();
              }

          },
          (response: any) => {
            this.ngxService.stop();
            
          }
        );
      } else{
        this.service.update(this.id, JSON.stringify(body)).subscribe(
          (response: any) => {
            if(!response.exitoso){
              this.ngxService.stop();
              this.notifier.notify('error', response.mensaje);
              } else {
              this.ngxService.stop();
              this.notifier.notify('success', response.mensaje);
              this.activeModal.dismiss();
              }

          },
          (response: any) => {
            this.ngxService.stop();
            
          }
        );
      }
    }
  }

  cargarForm(){
    this.form.patchValue({
      titulo: this.banner.titulo,
      url: this.banner.url

    });
    this.base64 =  this.banner.imagen;
    this.base64Movil = this.banner.imagen;
/*     this.fileType = this.banner.tipoArchivo;
    this.fileName = this.banner.titulo + '.'+this.banner.tipoArchivo; */
  }

}
