import { DatePipe } from '@angular/common';
import { Pipe, PipeTransform } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';

@Pipe({
  name: 'PipeFecha'
})
export class PipeFecha implements PipeTransform {

  transform(query: string): unknown {
    if(query === '' ) {
      return query;
    } else if(query === undefined ){
      return query;
    } else if(query === null){
      return query;
    }else {
      if(query.length === 10){
        let fecha = query.split('-');
        return fecha[2] +'/'+ fecha[1]+'/'+fecha[0];
      } else {
        let fecha = query.slice(0,10).split('-');
        return fecha[2] +'/'+ fecha[1]+'/'+fecha[0];
      }
    
    }
  }

}
@Pipe({
  name: 'thousandsPipe'
})

export class ThousandsPipe implements PipeTransform {

  public transform(value: any) {
    if(value === undefined){
      return '';
    } else if(value === '' || value === null){
      return '';
    } else {
      return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
    }

  }
}

@Pipe({
  name: 'dateFilter'
})
export class DateFilterPipe implements PipeTransform {

  constructor(private datePipe: DatePipe) {
  }

  transform(value: any): any {
    let format = 'dd/mm/yyyy';

    if (!value) {
      return '';
    }
    if (value === undefined || value === 'undefined') {
      return '';
    }
    format = format || 'short';
    return value && typeof(value) === 'object' ? this.datePipe.transform(value, format) : '';


  }

}

@Pipe({
    name: 'TrustHtml',
    pure: true // This allows the pipe to only run once
})
export class TrustHtmlPipe implements PipeTransform {

    constructor(private sanitizer: DomSanitizer) {
    }

    transform(pUnsafe: string) {
        return this.sanitizer.bypassSecurityTrustHtml(pUnsafe);
    }
}