import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Servers } from '../config/api';
import { TokenStorageService } from './token-storage.service';

@Injectable({
  providedIn: 'root'
})
export class DiasRecoleccionService {

  private recoleccion = Servers.ECOLOGIA.baseUrl + 'dias-recoleccion';
  httpOptions: {};
  constructor(private http: HttpClient, private tokenStorageService: TokenStorageService) {
    this.httpOptions = {
      headers: new HttpHeaders({ 'Content-Type': 'application/json' })
    };
  }

  getAll(params:any): Observable<any> {
    return this.http.get(this.recoleccion, {params: params});
  }

  getByIdMunicipio(id: any): Observable<any> {
    return this.http.get(this.recoleccion + '/municipio/' + id, this.httpOptions);
  }
  getById(id: any): Observable<any> {
    return this.http.get(this.recoleccion + '/' + id, this.httpOptions);
  }
  save(recoleccion: any): Observable<any> {
    return this.http.post(this.recoleccion, recoleccion, this.httpOptions);
  }

  update(idRecoleccion: number, recoleccion: any): Observable<any> {
    return this.http.put(this.recoleccion + '/actualizar/' + idRecoleccion, recoleccion, this.httpOptions);
  }


  desactivar(id: string): Observable<any> {
    return this.http.put(this.recoleccion + '/desactivar/' + id, this.httpOptions);
  }

  getContactos(): Observable<any> {
    return this.http.get(Servers.ECOLOGIA.baseUrl  + 'empresa', this.httpOptions);
  }

}
