import {Component, OnInit} from '@angular/core';
import {CuentacatastralService} from "../../services/cuentacatastral.service";
import {NgxUiLoaderService} from "ngx-ui-loader";
import {MunicipiosService} from "../../services/municipios.service";
import {NotifierService} from "angular-notifier";

@Component({
  selector: 'app-sub-zona',
  templateUrl: './sub-zona.component.html',
  styleUrls: ['./sub-zona.component.scss']
})
export class SubZonaComponent implements OnInit {
  subzonas: any[] = [];
  totalRecords = 0;
  pagina: number = 1;
  cantidad: number = 10;
  sortBy: string = 'idMunicipio';
  sortOrder: string = 'asc';

  constructor(private ngxService: NgxUiLoaderService, private municipioService: MunicipiosService, private notifier: NotifierService) {
  }

  ngOnInit(): void {
    this.getMunicipios(0)
  }

  getMunicipios(page: number) {
    this.pagina = page + 1;
    const params = {
      cantidad: this.cantidad,
      pagina: this.pagina,
      sortBy: "idMunicipio",
      sortOrder: "asc"
    }
    this.ngxService.start();
    this.municipioService.getAll(params).subscribe(
      (response: any) => {
        if (response) {
          this.ngxService.stop();
          this.totalRecords = response.total;
          this.subzonas = response.lista;
        } else {
          this.totalRecords = 0;
          this.subzonas = [];
        }
      },
      (response: any) => {
        this.ngxService.stop();
        
      }
    )
  }
}
