import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { Mensaje } from 'src/app/config/constantes';
import { MunicipiosService } from 'src/app/services/municipios.service';

@Component({
  selector: 'app-form-municipio',
  templateUrl: './form-municipio.component.html',
  styleUrls: ['./form-municipio.component.scss']
})
export class FormMunicipioComponent implements OnInit {
  titulo: any;
  id: any;
  form: FormGroup;
  activatedView = false;
  activatedEdit = false;
  submit = false;
  tituloBoton = 'Guardar';
  get f() {
    return this.form.controls;
  }
  constructor(private service: MunicipiosService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService, private notifier: NotifierService,
    private formBuilder: FormBuilder, private route: ActivatedRoute) {
      this.activatedView = this.router.url.includes('/visualizar');
      this.activatedEdit = this.router.url.includes('/editar');
      this.form = this.formBuilder.group(
        {
          nombre: [{value: null, disabled: this.activatedView}, Validators.required],
          codigo: [{value: null, disabled: this.activatedView}, Validators.required],
        }
      );

  }


  ngOnInit(): void {
    this.titulo = 'Agregar';
    this.id = this.route.snapshot.paramMap.get('id');
    this.ngxService.stop();
    if (this.id != null){
        this.ngxService.stop();
        if (this.activatedView){
          this.titulo = 'Ver';
        }else{
          this.tituloBoton = 'Actualizar';
          this.titulo = 'Editar';
        }
        this.service.getById(parseInt(this.id)).subscribe(
          (response: any) =>  {
            this.ngxService.stop();
            this.form.patchValue({
              nombre: response.nombre,
              codigo: response.codigo,
            });
          },
          (error: any) => {
            this.ngxService.stop();
            this.notifier.notify('error', error.error.mensaje);
          }
      );
    }
  }
  guardar(){
    this.submit = true;
    if (this.form.invalid) {
      this.notifier.notify('error', Mensaje.VERIFICAR_CAMPOS);
      return;
    } else{
      if(!this.activatedEdit){
        this.ngxService.start();
        let body = {
          nombre: this.form.controls.nombre.value,
          codigo: this.form.controls.codigo.value,
        }
        this.service.save(body).subscribe(
          (response: any) => {
            if(!response.exitoso){
              this.ngxService.stop();
              this.notifier.notify('error', response.mensaje);
             } else {
              this.ngxService.stop();
              this.notifier.notify('success', response.mensaje);
              this.router.navigate(['/gestion-interna/municipios']);
             }

          },
          (response: any) => {
            this.ngxService.stop();
            
          }

        )
      } else {
        this.ngxService.start();
        let body = {
          nombre: this.form.controls.nombre.value,
          codigo: this.form.controls.codigo.value
        }
        this.service.update(this.id, body).subscribe(
          (response: any) => {
             if(!response.exitoso){
              this.ngxService.stop();
              this.notifier.notify('error', response.mensaje);
             } else {
              this.ngxService.stop();
              this.notifier.notify('success', response.mensaje);
              this.router.navigate(['/gestion-interna/municipios']);
             }
          },
          (response: any) => {
            this.ngxService.stop();
            
          }

        )
      }
    }

  }
}
