import { Component, OnInit } from '@angular/core';import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { DeviceDetectorService } from 'ngx-device-detector';
import { PrimeNGConfig } from 'primeng/api';
import { AuthService } from './services/auth.service';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent  implements OnInit {
  isMobile = false;
  title = 'ecologia-verde-front';
  datosuser: any = {};
  posicionActual: any =[];
  constructor(private deviceService: DeviceDetectorService, public router: Router,
      private activatedRoute: ActivatedRoute, private config: PrimeNGConfig,
      private locationService: AuthService, ) {
    this.isMobile = this.deviceService.isMobile();
    this.config.setTranslation({
      dayNames: ["Domingo", "Lunes", "Martes", "Miercoles", "Jueves", "Viernes", "Sábado"],
      dayNamesShort: ["Dom", "Lun", "Mar", "Mie", "Jue", "Vie", "Sáb"],
      dayNamesMin: ["Do", "Lu", "Ma", "Mi", "Ju", "Vi", "Sá"],
      monthNames: ["Enero", "Febrero", "Marzo", "Abril", "Mayo", "Junio", "Julio", "Agosto", "Septiembre", "Octubre", "Noviembre", "Diciembre"],
      monthNamesShort: ["Ene", "Feb", "Mar", "Abr", "May", "Jun", "Jul", "Ago", "Sep", "Oct", "Nov", "Dic"],
      today: "Hoy",
      clear: "Limpiar",
    });
    router.events.subscribe((event: any) => {
      if (event instanceof NavigationEnd) {
        if (localStorage.getItem('auth-user')) {
          this.datosuser = JSON.parse(localStorage.getItem('auth-user')|| '{}');
        }
      }
    });
  }
  ngOnInit(): void {
    this.getLocation();
  }
  getLocation() {
    this.locationService.getPosition().then(pos => {
      this.posicionActual = pos;
       /* almacenar la ubicacion del usuario en el localStorage */
       localStorage.setItem('latitud',this.posicionActual.lat);
       localStorage.setItem('longitud',this.posicionActual.lng);
    });
  
  }
}
