import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {NgxUiLoaderService} from "ngx-ui-loader";
import {MunicipiosService} from "../../../services/municipios.service";
import {NotifierService} from "angular-notifier";
import {ActivatedRoute, Router} from "@angular/router";
import { CuentacatastralService } from 'src/app/services/cuentacatastral.service';
import {UsuariosService} from "../../../services/usuarios.service";
import {Mensaje} from "../../../config/constantes";

@Component({
  selector: 'app-detalle-cuenta-catastral',
  templateUrl: './detalle-cuenta-catastral.component.html',
  styleUrls: ['./detalle-cuenta-catastral.component.scss']
})
export class DetalleCuentaCatastralComponent implements OnInit {
  municipios: any[] = [];
  form: FormGroup;
  activatedView = false;
  activatedEdit = false;
  submit = false;
  id: any;
  tituloBoton = 'Guardar';
  options: any;
  overlays: any;
  get f() {
    return this.form.controls;
  }
  constructor(private ngxService: NgxUiLoaderService,
              private municipioService: MunicipiosService,
              private cuentacatastralService: CuentacatastralService,
              private usuariosService: UsuariosService,
              private notifier: NotifierService,
              public router: Router,
              private route: ActivatedRoute,
              private formBuilder: FormBuilder) {
    this.activatedView = this.router.url.includes('/visualizar');
    this.activatedEdit = this.router.url.includes('/editar');
    this.form = this.formBuilder.group(
      {
        nombre: [{value: null, disabled: this.activatedView}, Validators.required],
        apellido: [{value: null, disabled: this.activatedView}, Validators.required],
        direccion: [{value: null, disabled: this.activatedView}, Validators.required],
        telefono: [{value: null, disabled: this.activatedView}, Validators.required],

        municipio: [{value: null, disabled: this.activatedView}, Validators.required],
        area: [{value: null, disabled: this.activatedView}, Validators.required],
        zona: [{value: null, disabled: this.activatedView}, Validators.required],
        subzona: [{value: null, disabled: this.activatedView}, Validators.required],

        activo: [{value: null, disabled: this.activatedView}, Validators.required],
      }
    );
  }

  ngOnInit(): void {
    this.getMunicipios();
    this.id = this.route.snapshot.paramMap.get('id');
    this.ngxService.stop();

    this.options = {
      center: { lat: -25.28646, lng: -57.60933841744386 },
      zoom: 10
    };
    this.overlays = [
      new google.maps.Marker({
        position: { lat: -25.28646, lng: -57.60933841744386 }, title: 'Ubicación',
        icon: {
          url: 'assets/img/icono-mapa.png',
          scaledSize: new google.maps.Size(35, 50),
          origin: new google.maps.Point(0, 0),
          labelOrigin: new google.maps.Point(23, 19)
        },
        draggable: true
      }),
    ];
    this.overlays[0].addListener('dragend', (function (notifier) {
      return function () {
      //  notifier.notify('success', Mensaje.UBICACION_ACTUALIZADA);
      };
    })(this.notifier));


    if (this.id != null){
      this.ngxService.stop();
      if (!this.activatedView){
        this.tituloBoton = 'Actualizar';
      }
      this.cuentacatastralService.getCuentaById(parseInt(this.id)).subscribe(
        (responseCuentaCatastral: any) =>  {
          this.ngxService.stop();
          this.form.patchValue({
            municipio: responseCuentaCatastral.idMunicipio,
            subzona: responseCuentaCatastral.idSubzona,
            zona: responseCuentaCatastral.idZona,
            area: responseCuentaCatastral.area,
            activo: responseCuentaCatastral.activo
            /*TODO: Completar los demas*/

          });
        },
        (error: any) => {
          this.ngxService.stop();
          this.notifier.notify('error', error.error.mensaje);
        }
      );
    }
  }

  getMunicipios() {
    this.ngxService.start();
    this.municipioService.getAll(undefined).subscribe(
      (response: any) => {
        this.ngxService.stop();
        this.municipios = response.lista;
      },
      (response: any) => {
        this.ngxService.stop();
        
      }
    )
  }
  handleMapClick(event:any) {
    this.form.patchValue({
      latitud:this.overlays[0].getPosition().lat(),
      longitud:this.overlays[0].getPosition().lng(),
    });
  }

  guardar(){
    this.submit = true;
    if (this.form.invalid) {
      this.notifier.notify('error', Mensaje.VERIFICAR_CAMPOS);
      return;
    } else{
      const body = {
        nombre: this.form.controls.nombre.value,
        apellido: this.form.controls.apellido.value,
        telefono: this.form.controls.telefono.value,
        direccion: this.form.controls.direccion.value,
        municipio:{idMunicipio: this.form.controls.municipio.value},
        area: this.form.controls.area.value,
        zona:{idZona: this.form.controls.zona.value},
        subzona:{idSubzona: this.form.controls.subzona.value},
        activo:{idSubzona: this.form.controls.activo.value},
        /*TODO: Completar los demas*/
      }

      if(!this.activatedEdit){
        this.ngxService.start();
        this.cuentacatastralService.agregarCuentaCatastral(this.id,body).subscribe(
          (response: any) => {
            if(!response.exitoso){
              this.ngxService.stop();
              this.notifier.notify('error', response.mensaje);
            } else {
              this.ngxService.stop();
              this.notifier.notify('success', response.mensaje);
              this.router.navigate(['/gestion-interna/usuarios']);
            }
          },
          (response: any) => {
            this.ngxService.stop();
            
          }
        )
      } else {
        this.ngxService.start();
        this.cuentacatastralService.editarCuentaCatastral(this.id, body).subscribe(
          (response: any) => {
            if(!response.exitoso){
              this.ngxService.stop();
              this.notifier.notify('error', response.mensaje);
            } else {
              this.ngxService.stop();
              this.notifier.notify('success', response.mensaje);
              this.router.navigate(['/gestion-interna/usuarios']);
            }
          },
          (response: any) => {
            this.ngxService.stop();
            
          }

        )
      }
    }

  }
}
