import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { Mensaje } from 'src/app/config/constantes';
import { CuentacatastralService } from 'src/app/services/cuentacatastral.service';
import { NotificacionesService } from 'src/app/services/notificaciones.service';
import { TokenStorageService } from 'src/app/services/token-storage.service';

@Component({
  selector: 'app-notificaciones',
  templateUrl: './notificaciones.component.html',
  styleUrls: ['./notificaciones.component.scss']
})
export class NotificacionesComponent implements OnInit {

  es: any = [];
  municipios = [];
  data: any = {};
  usuarios = [];
  form: FormGroup;
  totalRecords =0;
  entidad:any = [];
  dataRelacion: any = {};
  page: number = 1;
  size: number = 10;
  submit: any = false;
  activatedView = false;
  activatedEdit = false;
  titulo = 'Agregar';
  id: any;
   constructor(private service: NotificacionesService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService, private notifier: NotifierService, private authService: TokenStorageService,
    private formBuilder: FormBuilder,   private route: ActivatedRoute, private serviceCuenta: CuentacatastralService) {
      this.activatedEdit = this.router.url.includes('/editar');
      this.activatedView = this.router.url.includes('/visualizar');
      this.form = this.formBuilder.group(
        {
          idNotificacion: [{value: null, disabled: this.activatedView}],
          municipio: [{value: null, disabled: this.activatedView}, Validators.required],
          area: [{value: null, disabled: this.activatedView}],
          zona: [{value: null, disabled: this.activatedView}],
          subzona: [{value: null, disabled: this.activatedView}],
          titulo: [{value: null, disabled: this.activatedView}, Validators.required],
          inicio: [{value: null, disabled: this.activatedView}, Validators.required],
          fin: [{value: null, disabled: this.activatedView}, Validators.required],
          hora: [{value: null, disabled: this.activatedView}, Validators.required],
          contenido:[{value: null, disabled: this.activatedView}, Validators.required]
        }
      );
  }

  ngOnInit(): void {
    this.titulo = 'Agregar';
    if( this.activatedEdit || this.activatedView){
      this.id = this.route.snapshot.paramMap.get('id');
    }
    if (this.id != null){
      this.ngxService.stop();
      this.service.getById(parseInt(this.id)).subscribe(
        (response: any) =>  {
          this.ngxService.stop();
         this.form.patchValue({
            municipio: response.municipio?response.municipio.idMunicipio:null,
            idNotificacion: response.idNotificacion,
            area: response.manzana,
            zona: response.zona,
            subzona: response.subZona,
            titulo: response.descripcion,
            hora: response.hora,
            contenido:response.contenido,
            inicio: this.parseFechaDatePicker(response.fechaCreacion),
            fin: this.parseFechaDatePicker(response.finVigencia),
           
          });
        },
        (error: any) => {
          this.ngxService.stop();
          this.notifier.notify('error', error.error.mensaje);
        }
    );
      if (this.activatedView){
        this.titulo = 'Ver';
        this.form.controls.contenido.disable();
      }else{
        this.titulo = 'Editar';
      }
  
    }
    this.getMunicipios();
    this.inicializarDatePicker();
    
  }
  inicializarDatePicker(){
    this.es = {
      firstDayOfWeek: 1,
      dayNames: [ "domingo","lunes","martes","miércoles","jueves","viernes","sábado" ],
      dayNamesShort: [ "dom","lun","mar","mié","jue","vie","sáb" ],
      dayNamesMin: [ "D","L","M","M","J","V","S" ],
      monthNames: [ "enero","febrero","marzo","abril","mayo","junio","julio","agosto","septiembre","octubre","noviembre","diciembre" ],
      monthNamesShort: [ "ene","feb","mar","abr","may","jun","jul","ago","sep","oct","nov","dic" ],
      today: 'Hoy',
      clear: 'Borrar'
    }
  }

  volver(){

  }
  getMunicipios() {
    this.ngxService.start();
    this.serviceCuenta.getMunicipios().subscribe(
      (response: any) => {
          this.ngxService.stop();
          this.municipios = response;
      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }
  parseFecha(valor:any) {

    if(valor === undefined || valor === null || valor === ''){
      return valor;
    }else {
      if( JSON.stringify(valor).includes('/')){
        var fechaList = valor.split('/');
        return fechaList[2]+'-'+fechaList[1]+'-'+fechaList[0];
      }else {
        var event = new Date(valor);
        let fecha = JSON.stringify(event);
        fecha = fecha.slice(1,11);
        //var fechaList = fecha.split('-');
        return fecha;
      }

      

    }
  }
  parseFechaDatePicker(valor:any) {
    if(valor === undefined || valor === null || valor === ''){
      return valor;
    }else {

      var fecha = valor.slice(0,10);
      var fechaList = fecha.split('-');
      return fechaList[2] + '/' + fechaList[1] + '/' +fechaList[0];
    }
  }
  agregar(){
    this.submit = true;
    if (this.form.invalid) {
      this.notifier.notify('error', Mensaje.VERIFICAR_CAMPOS);
      return;
    } else{
     if( !this.activatedEdit){
      var body ={
        "zona":this.form.controls.zona.value,
        "manzana":this.form.controls.area.value,
        "subZona": this.form.controls.subzona.value,
        "descripcion":this.form.controls.titulo.value,
        "inicioVigencia": this.parseFecha(this.form.controls.inicio.value) + ' 00:00:00',
        "finVigencia": this.parseFecha(this.form.controls.fin.value) + ' 23:59:59',
        "hora":this.form.controls.hora.value,
        "contenido":this.form.controls.contenido.value,
        "idMunicipio":this.form.controls.municipio.value,
      }
  
    this.ngxService.start();
    this.service.agregar(body).subscribe(
      (response: any) => {
          this.ngxService.stop();
          if(response.exitoso){
            this.notifier.notify('success', response.mensaje);
            this.router.navigate(['/gestion-interna/notificaciones']);
          }else {
            this.notifier.notify('error', response.mensaje);
          }
         
      },
      (response: any) => {
        this.ngxService.stop();
        
      }
  
      )
     } else {
      var bodyEditar = {
        "idNotificacion":this.form.controls.idNotificacion.value,
        "zona":this.form.controls.zona.value,
        "manzana":this.form.controls.area.value,
        "subZona": this.form.controls.subzona.value,
        "descripcion":this.form.controls.titulo.value,
        "inicioVigencia": this.parseFecha(this.form.controls.inicio.value) + ' 00:00:00',
        "finVigencia": this.parseFecha(this.form.controls.fin.value) + ' 23:59:59',
        "hora":this.form.controls.hora.value,
        "contenido":this.form.controls.contenido.value,
        "idMunicipio":this.form.controls.municipio.value,
      }
  
    this.ngxService.start();
    this.service.modificar(bodyEditar).subscribe(
      (response: any) => {
          if(response.exitoso){
            this.ngxService.stop();
            this.notifier.notify('success', response.mensaje);
            this.router.navigate(['/gestion-interna/notificaciones']);
          }else{
            this.ngxService.stop();
            
          }
          
      },
      (response: any) => {
        this.ngxService.stop();
        
      }
  
    )
     }
    }

 }
 onComplete(){
  var hora = this.form.controls.hora.value.split(':');
  this.form.patchValue({

    hora: hora[0] + ":00",

   
  });
  }
}
