import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { CuentacatastralService } from 'src/app/services/cuentacatastral.service';
import { NotificacionesService } from 'src/app/services/notificaciones.service';
import { UsuariosService } from 'src/app/services/usuarios.service';

@Component({
  selector: 'app-detalle-notificacion',
  templateUrl: './detalle-notificacion.component.html',
  /* template: '<div [innerHTML]="notificacion.contenido"></div>', */
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./detalle-notificacion.component.scss']
})
export class DetalleNotificacionComponent implements OnInit {
  @Input() id: any = null;
  notificacion:any = { titulo:'', contenido: ''};
  notificaciones: any = [];
  form: FormGroup;
  constructor(public activeModal: NgbActiveModal,private service: NotificacionesService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService,
    private formBuilder: FormBuilder, private notifier: NotifierService, private route: ActivatedRoute, private serviceCuenta: CuentacatastralService) {
      this.form = this.formBuilder.group(
        {
          titulo:[null],
          contenido: [null]
        }
      );
    }

  ngOnInit(): void {
    this.id = this.route.snapshot.paramMap.get('id');
    console.log(this.id);
    this.getEntidad();

  }
  getEntidad() {
    this.ngxService.start();
    this.service.getById(this.id).subscribe(
      (response: any) => {
        this.ngxService.stop();
        if(response != null){
          this.notificaciones = response;
          this.cargarForm();
        }

      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }
  cargarForm(){
    this.notificacion = { titulo: this.notificaciones.descripcion, contenido:  this.notificaciones.contenido};
  }
}
