import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { deleteEmptyData } from 'src/app/config/app-utils';
import { Mensaje } from 'src/app/config/constantes';
import { UsuariosService } from 'src/app/services/usuarios.service';

@Component({
  selector: 'app-listar-usuarios',
  templateUrl: './listar-usuarios.component.html',
  styleUrls: ['./listar-usuarios.component.scss']
})
export class ListarUsuariosComponent implements OnInit {
  totalRecords =0;
  entidad:any = [];
  dataRelacion: any = {};
  page: number = 1;
  size: number = 10;
  displayPassword: boolean = false;
  idUsuarioPass: any = null;
  form: FormGroup;
  get f() {
    return this.form.controls;
  }
  submit = false;
   constructor(private usuarioService: UsuariosService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService,  private formBuilder: FormBuilder, private notifier: NotifierService) {
      this.form = this.formBuilder.group(
        {
          password: [{value: null, disabled:false}, Validators.required],
        }
      );
  }



  ngOnInit(){
    this.getUsuarios();
  }
   getUsuarios() {
    const params = {
      cantidad: 10,
      pagina: this.page,
      sortBy:"idUsuario",
      sortOrder: "asc",
      //filtros: JSON.stringify(deleteEmptyData())
    }
    this.ngxService.start();
    this.usuarioService.getAll(params).subscribe(
      (response: any) => {
        if(response){
          this.ngxService.stop();
          this.totalRecords = response.total;
          this.entidad = response.lista;
        }else {
          this.totalRecords = 0;
          this.entidad = [];
        }

      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }

  agregar(){
    this.router.navigate(['/gestion-interna/usuario/agregar/']);
  }

  ver(id:any){
    this.router.navigate(['/gestion-interna/usuario/visualizar/'+id]);
  }
  editar(id:any){
    this.router.navigate(['/gestion-interna/usuario/editar/'+id]);
  }
  eliminarUsuario(item:any) {
    this.ngxService.start();
    if (item.idUsuario != null) {
      let body ={
        nombre: item.nombre,
        apellido: item.apellido,
        correo: item.correo,
        direccion: item.direccion,
        password: item.password,
        rol:{idRol: item.rol},
        telefono: item.telefono,
      }
      this.usuarioService.delete(item.idUsuario, body).subscribe(
        (result: any) => {
          this.ngxService.stop();
          this.entidad = [];
          this.totalRecords = 0;
          this.notifier.notify('success', result.mensaje);
          this.getUsuarios();

        },
        (error: any) => {
          this.ngxService.stop();
          this.notifier.notify('error', error.error.mensaje);
        }

      )
    } else {
      this.messageService.add({ severity: 'warning', summary: '¡Advertencia!', detail: 'Seleccione un registro' });
    }

  }
  eliminar(item:any) {
    this.confirmationService.confirm({
      message: '¿Está seguro que desea eliminar al usuario '+ item.nombre + '?',
      header: 'Confirmar Eliminación',
      icon: 'pi pi-info-circle',
      accept: () => {
        this.eliminarUsuario(item);
      },
      reject: () => {

      }
    });
  }
  paginate(event: any) {
    this.page = event.page +1;
    this.entidad = [];
    this.getUsuarios();
  }
  changePass(data: any){
    this.idUsuarioPass = data;
    this.displayPassword = true;
  }
  cerrarPass(){
    this.idUsuarioPass = null;
    this.displayPassword = false;
  }
  cambiarPass(){
    this.submit = true;
    if (this.form.invalid) {
      this.notifier.notify('error', Mensaje.VERIFICAR_CAMPOS);
      return;
    } else{
      this.ngxService.start();
        let body = {
          password: this.form.controls.password.value,
        }
        this.usuarioService.cambiarPassword(this.idUsuarioPass,body).subscribe(
          (response: any) => {
            if(!response.exitoso){
              this.ngxService.stop();
              this.notifier.notify('error', response.mensaje);
             // this.cerrarPass();
             } else {
              this.cerrarPass();
              this.ngxService.stop();
              this.notifier.notify('success', response.mensaje);
             }

          },
          (response: any) => {
            this.ngxService.stop();
            
          }

        )
    }
  }
}
