import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { MunicipiosService } from 'src/app/services/municipios.service';

@Component({
  selector: 'app-listar-municipio',
  templateUrl: './listar-municipio.component.html',
  styleUrls: ['./listar-municipio.component.scss']
})
export class ListarMunicipioComponent implements OnInit {

  totalRecords =0;
  entidad:any = [];
  dataRelacion: any = {};
  page: number = 1;
  size: number = 10;
   constructor(private municipioService: MunicipiosService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService, private notifier: NotifierService) {
  }



  ngOnInit(){
    this.getEntidad();
  }
   getEntidad() {
    const params = {
      cantidad: 10,
      pagina: this.page,
      sortBy:"idMunicipio",
      sortOrder: "asc",
      //filtros: JSON.stringify(deleteEmptyData())
    }
    this.ngxService.start();
    this.municipioService.getAll(params).subscribe(
      (response: any) => {
        if(response){
          this.ngxService.stop();
          this.totalRecords = response.total;
          this.entidad = response.lista;
        }else {
          this.totalRecords = 0;
          this.entidad = [];
        }

      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }

  agregar(){
    this.router.navigate(['/gestion-interna/municipio/agregar']);
  }

  ver(id:any){
    this.router.navigate(['/gestion-interna/municipio/visualizar/'+id]);
  }
  editar(id:any){
    this.router.navigate(['/gestion-interna/municipio/editar/'+id]);
  }
  eliminarMunicipio(id:any) {
    this.ngxService.start();
    if (id != null) {
      this.municipioService.delete(id).subscribe(
        (result: any) => {
          this.ngxService.stop();
          this.entidad = [];
          this.totalRecords = 0;
          this.notifier.notify('success', result.mensaje);
          this.getEntidad();

        },
        (error: any) => {
          this.ngxService.stop();
          this.notifier.notify('error', error.error.mensaje);
        }

      )
    } else {
      this.messageService.add({ severity: 'warning', summary: '¡Advertencia!', detail: 'Seleccione un registro' });
    }

  }
  eliminar(id:any) {
    this.confirmationService.confirm({
      message: '¿Está seguro que desea eliminar el Municipio ?',
      header: 'Confirmar Eliminación',
      icon: 'pi pi-info-circle',
      accept: () => {
        this.eliminarMunicipio(id);
      },
      reject: () => {

      }
    });
  }
  paginate(event: any) {
    this.page = event.page +1;
    this.entidad = [];
    this.getEntidad();
  }

}
