import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { deleteEmptyData } from 'src/app/config/app-utils';
import { Mensaje } from 'src/app/config/constantes';
import { CuentacatastralService } from 'src/app/services/cuentacatastral.service';
import { DiasRecoleccionService } from 'src/app/services/dias-recoleccion.service';
import { ReclamosService } from 'src/app/services/reclamos.service';
import { TokenStorageService } from 'src/app/services/token-storage.service';
declare var $: any;
declare const google: any;
@Component({
  selector: 'app-detalle-reclamo',
  templateUrl: './detalle-reclamo.component.html',
  styleUrls: ['./detalle-reclamo.component.scss']
})
export class DetalleReclamoComponent implements OnInit {
  submit = false;
  overlays: any;
  cuenta: any= [];
  get f() {
    return this.form.controls;
  }
  options: any;
  data: any = {};
  form: FormGroup;
  id: any= null;
  reclamo: any = [];
  archivos: any = [];
   constructor(private service: ReclamosService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService, private notifier: NotifierService, private authService: TokenStorageService,
    private formBuilder: FormBuilder, private modalService: NgbModal, private cuentaService: CuentacatastralService,private route: ActivatedRoute) {
      this.id = this.route.snapshot.paramMap.get('id');
      this.obtenerReclamoAdjunto();
      this.form = this.formBuilder.group(
        {
          descripcion: [{value: null, disabled: true}],
          fecha: [{value: null, disabled: true}],
          cliente: [{value: null, disabled: true}],

          comentario: [{value: null, disabled: true}],
          fechaRespuesta: [{value: null, disabled: true}],
          clienteRespuesta: [{value: null, disabled: true}]
        }
      );
  }

  ngOnInit(): void {
    this.obtenerReclamo();
    
    this.options = {
      center: { lat: -25.28646, lng: -57.60933841744386 },
      zoom: 10
    };
    this.overlays = [
      new google.maps.Marker({
        position: { lat: -25.28646, lng: -57.60933841744386 }, title: 'Ubicación',
        icon: {
          url: 'assets/img/icono-mapa.png',
          scaledSize: new google.maps.Size(35, 50),
          origin: new google.maps.Point(0, 0),
          labelOrigin: new google.maps.Point(23, 19)
        },
        draggable: true
      }),
    ];
    this.overlays[0].addListener('dragend', (function (notifier) {
      return function () {
      //  notifier.notify('success', Mensaje.UBICACION_ACTUALIZADA);
      };
    })(this.notifier));
  
  }

  obtenerReclamoAdjunto(){
    this.ngxService.start();
    this.service.getByIdAdjuntos(parseInt(this.id)).subscribe(
      (response: any) =>  {
        this.ngxService.stop();
        if(response.exitoso){
          this.archivos = response.data;
        }
      },
      (error: any) => {
        this.ngxService.stop();
        this.notifier.notify('error', error.error.mensaje);
      }
  );
  
  }
  obtenerReclamo(){
    this.service.getById(this.id).subscribe(
      (response: any) => {
          this.reclamo = response;
          this.cargarForm();
      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }
  cargarForm(){
    this.form.patchValue({
      descripcion: this.reclamo.descripcion,
      fecha: this.parseFecha(this.reclamo.fechaReclamo),
      cliente: this.reclamo.usuario.nombre + ' ' +  this.reclamo.usuario.apellido,

      comentario: this.reclamo.respuestaReclamo,
      fechaRespuesta: this.parseFecha(this.reclamo.fechaRespuesta),
      clienteRespuesta: this.reclamo.usuarioRespuesta != undefined && this.reclamo.usuarioRespuesta != null ? this.reclamo.usuarioRespuesta.nombre + ' ' +  this.reclamo.usuarioRespuesta.apellido : '',
    });
    this.ubicarMarker();
   
  }

  parseFecha(valor: any){
   if(valor === undefined){
    return '';
   }else {
    if(valor === null || valor === ''){
      return valor;
  } else {
    let fecha = valor.slice(0,10);
    fecha = fecha.split('-');
    return fecha[2] + '/' + fecha[1] + '/'+fecha[0];
  }
   }
  }
  ubicarMarker(){
    this.overlays = [
      new google.maps.Marker({
        position: { lat:  this.reclamo.latitud, lng: this.reclamo.longitud}, title: 'Ubicación',
        icon: {
          url: 'assets/img/icono-mapa.png',
          scaledSize: new google.maps.Size(35, 50),
          origin: new google.maps.Point(0, 0),
          labelOrigin: new google.maps.Point(23, 19)
        },
        draggable: false

      }),
    ];
  }

  dragend() {
    this.notifier.notify('success', Mensaje.UBICACION_ACTUALIZADA);
  }
  handleMapClick(event:any) {
    this.form.patchValue({
      latitud:this.overlays[0].getPosition().lat(),
      longitud:this.overlays[0].getPosition().lng(),
    });
  }


  descargar(item:any){ 
    let archivoCodificado;
    /* if(item.archivo != undefined && item.archivo != null &&
      item.archivo != ''){
        archivoCodificado = item.archivo.split('base64,');
        if(archivoCodificado != undefined && archivoCodificado != null &&
          archivoCodificado.length > 0){
            archivoCodificado = archivoCodificado[1];
        }
    } */
    this.decodeBase64(item.archivo,item.nombreArchivo);
  }
  decodeBase64(file: any, nombre:any ) {
    const blobData = this.convertBase64ToBlobData(file);
    if (window.navigator && window.navigator.msSaveOrOpenBlob) { // IE
      window.navigator.msSaveOrOpenBlob(blobData, nombre);
    } else { // chrome
      const blob = new Blob([blobData], { type: 'application/octet-stream' });
      const url = window.URL.createObjectURL(blob);
      // window.open(url);
      const link = document.createElement('a');
      link.href = url;
      link.download = nombre;
      link.click();
    }
  }
  convertBase64ToBlobData(base64Data: string, contentType: string= 'application/octet-stream', sliceSize= 512) {
    const byteCharacters = atob(base64Data);
    const byteArrays = [];

    for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
      const slice = byteCharacters.slice(offset, offset + sliceSize);

      const byteNumbers = new Array(slice.length);
      for (let i = 0; i < slice.length; i++) {
        byteNumbers[i] = slice.charCodeAt(i);
      }

      const byteArray = new Uint8Array(byteNumbers);

      byteArrays.push(byteArray);
    }

    const blob = new Blob(byteArrays, { type: contentType });
    return blob;
  }
 
}
