import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { Mensaje } from 'src/app/config/constantes';
import { CuentacatastralService } from 'src/app/services/cuentacatastral.service';
import { UsuariosService } from 'src/app/services/usuarios.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

declare var $: any;
declare const google: any;
@Component({
  selector: 'app-cuenta-catastral-ver-mapa',
  templateUrl: './cuenta-catastral-ver-mapa.component.html',
  styleUrls: ['./cuenta-catastral-ver-mapa.component.scss']
})
export class CuentaCatastralVerMapaComponent implements OnInit {
  overlays: any;
  options: any;
  @Input() latitud: any = null;
  @Input() longitud: any = null;
  constructor(public activeModal: NgbActiveModal,private service: UsuariosService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService, private notifier: NotifierService,
    private formBuilder: FormBuilder, private route: ActivatedRoute, private serviceCuenta: CuentacatastralService) { }

  ngOnInit(): void {
    console.log(this.latitud)
    this.options = {
      center: { lat: this.latitud != null ? this.latitud: -25.28646, lng: this.longitud != null ? this.longitud: -57.60933841744386 },
      zoom: 16
    };
    this.overlays = [
      new google.maps.Marker({
        position: { lat: this.latitud != null ? this.latitud: -25.28646, lng: this.longitud != null ? this.longitud: -57.60933841744386 }, title: 'Ubicación',
        icon: {
          url: 'assets/img/icono-mapa.png',
          scaledSize: new google.maps.Size(35, 50),
          origin: new google.maps.Point(0, 0),
          labelOrigin: new google.maps.Point(23, 19)
        },
        draggable: false
      }),
    ];
    this.overlays[0].addListener('dragend', (function (notifier) {
      return function () {
      //  notifier.notify('success', Mensaje.UBICACION_ACTUALIZADA);
      };
    })(this.notifier));
  }
  dragend() {
    this.notifier.notify('success', Mensaje.UBICACION_ACTUALIZADA);
  }
  handleMapClick(event:any) {
    $('#latitud').val(this.overlays[0].getPosition().lat());
    $('#longitud').val(this.overlays[0].getPosition().lng());
  }

}
