import { DiasRecoleccionService } from '../../../services/dias-recoleccion.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { CuentacatastralService } from 'src/app/services/cuentacatastral.service';
import { TokenStorageService } from 'src/app/services/token-storage.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { deleteEmptyData } from 'src/app/config/app-utils';

@Component({
  selector: 'app-dias-de-recoleccion',
  templateUrl: './dias-de-recoleccion.component.html',
  styleUrls: ['./dias-de-recoleccion.component.scss']
})
export class DiasdeRecoleccionComponent implements OnInit {

  municipios = [];
  data: any = {};
  form: FormGroup;
  totalRecords =0;
  entidad:any = [];
  dataRelacion: any = {};
  page: number = 1;
  size: number = 10;
  filtros: any = null;
  dias= [{id:'lunes', nombre: 'Lunes'},{id:'martes', nombre: 'Martes'},{id:'miercoles', nombre: 'Miércoles'},
  {id:'jueves', nombre: 'Jueves'},{id:'viernes', nombre: 'Viernes'},{id:'sabado', nombre: 'Sábado'},{id:'domingo', nombre: 'Domingo'}]
   constructor(private service: DiasRecoleccionService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService, private notifier: NotifierService, private authService: TokenStorageService,
    private formBuilder: FormBuilder, private modalService: NgbModal, private cuentaService: CuentacatastralService) {

      this.form = this.formBuilder.group(
        {
          dia: [null],
          municipio: [null]
        }
      );
  }

  ngOnInit(): void {
    this.getMunicipios();
    this.getEntidad();
  }
  getEntidad() {
    const params = {
      cantidad: 10,
      pagina: this.page,
      sortBy:"idDiaRecoleccion",
      sortOrder: "asc",
      filtros: this.filtros === null ? '' : JSON.stringify(deleteEmptyData(this.filtros))
    }
    this.ngxService.start();
    this.service.getAll(params).subscribe(
      (response: any) => {
        if(response){
          this.ngxService.stop();
          this.totalRecords = response.total;
          this.entidad = response.lista;
        }else {
          this.totalRecords = 0;
          this.entidad = [];
        }

      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }
  filtrar(){
    this.filtros = {
      "dia":this.form.controls.dia.value,
      "municipio.idMunicipio":this.form.controls.municipio.value
    }
    this.getEntidad();

  }
  getMunicipios() {
    this.ngxService.start();
    this.cuentaService.getMunicipios().subscribe(
      (response: any) => {
          this.ngxService.stop();
          this.municipios = response;
      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }

  limpiar(){
    this.filtros = null;
    this.totalRecords = 0;
    this.entidad = [];
    this.form.reset();
    this.getEntidad();
  }
  ver(id:any){
    this.router.navigate(['/gestion-interna/dia-recoleccion/visualizar/'+id]);
  }
  editar(id:any){
    this.router.navigate(['/gestion-interna/dia-recoleccion/editar/'+id]);
  }
  eliminarEntidad(item:any) {
    this.ngxService.start();
    if (item.idDiaRecoleccion != null) {
      this.service.desactivar(item.idDiaRecoleccion).subscribe(
        (result: any) => {
          this.ngxService.stop();
          this.entidad = [];
          this.totalRecords = 0;
          this.notifier.notify('success', result.mensaje);
          this.getEntidad();

        },
        (error: any) => {
          this.ngxService.stop();
          this.notifier.notify('error', error.error.mensaje);
        }

      )
    } else {
      this.messageService.add({ severity: 'warning', summary: '¡Advertencia!', detail: 'Seleccione un registro' });
    }

  }
  eliminar(item:any) {
    this.confirmationService.confirm({
      message: '¿Está seguro que desea eliminar el día de recolección?',
      header: 'Confirmar Eliminación',
      icon: 'pi pi-info-circle',
      accept: () => {
        this.eliminarEntidad(item);
      },
      reject: () => {

      }
    });
  }
  paginate(event: any) {
    this.page = event.page +1;
    this.entidad = [];
    this.getEntidad();
  }


}
