import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { Mensaje } from 'src/app/config/constantes';
import { AuthService } from 'src/app/services/auth.service';

@Component({
  selector: 'app-restablecer-contrasenha',
  templateUrl: './restablecer-contrasenha.component.html',
  styleUrls: ['./restablecer-contrasenha.component.scss']
})
export class RestablecerContrasenhaComponent implements OnInit {

  hide = true;
  form: FormGroup;
  submit = false;
  private notifier: NotifierService;
  isLoggedIn = false;
  isLoginFailed = false;
  errorMessage = '';
  token = null;
  user = '';
  get f() {
    return this.form.controls;
  }
  constructor(public router: Router, private formBuilder: FormBuilder, private ngxService: NgxUiLoaderService,
    private notifierService: NotifierService, private service: AuthService, private route: ActivatedRoute) {
    this.form = this.formBuilder.group(
      {
        password: [null, Validators.required],
        passwordNew: [null, Validators.required],
      }
    );
    this.notifier = notifierService;

  }

  ngOnInit(): void {
    this.token =  this.route.snapshot.queryParams['token'];
    this.user = this.route.snapshot.queryParams['user'];
  }
  enviar(){
    if (this.form.valid) {
      if(this.form.controls.password.value === this.form.controls.passwordNew.value){
        var params ={

          correo:this.user,
          password:this.form.controls.password.value,
          validadorpass:this.form.controls.passwordNew.value,
          token:this.token

        }
        this.ngxService.start();
        this.service.resetPass(params).subscribe(
          (response: any) => {
            this.ngxService.stop();
            if(response.exitoso){
              this.notifier.notify('success', response.mensaje);
              this.router.navigate(['/login']);
            }else {
              this.notifier.notify('error', response.mensaje);
            }
    
          },
          (response: any) => {
            this.ngxService.stop();
            
          }
        )
      } else{
        this.notifier.notify('error', Mensaje.NO_COINCIDEN);
      }
    } else {
      this.notifier.notify('error', Mensaje.VERIFICAR_CAMPOS);
    }
  }

}
