import { TokenStorageService } from '../../../services/token-storage.service';
import { CuentacatastralService } from '../../../services/cuentacatastral.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { deleteEmptyData } from 'src/app/config/app-utils';
import { Paginator } from 'primeng/paginator';
import { CuentaCatastralAgregarComponent } from '../../clientes/cuenta-catastral-agregar/cuenta-catastral-agregar.component';

@Component({
  selector: 'app-cuenta-catastral',
  templateUrl: './cuenta-catastral.component.html',
  styleUrls: ['./cuenta-catastral.component.scss']
})
export class CuentaCatastralComponent implements OnInit {
  @ViewChild('p', {static: false}) paginator: Paginator | any;
  municipios = [];
  data: any = {};
  usuarios = [];
  form: FormGroup;
  totalRecords =0;
  entidad:any = [];
  dataRelacion: any = {};
  page: number = 1;
  size: number = 10;
  filtros: any = null;
   constructor(private service: CuentacatastralService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService, private notifier: NotifierService, private authService: TokenStorageService,
    private formBuilder: FormBuilder, private modalService: NgbModal) {

      this.form = this.formBuilder.group(
        {
          nombre: [null],
          apellido: [null],
          cuentaCatastral: [null],
          municipio: [null]
        }
      );
  }

  ngOnInit(): void {
    this.getMunicipios();
    this.getEntidad();
  }
  getEntidad() {
    const params = {
      cantidad: 10,
      pagina: this.page,
      sortBy:"idCuentaCatastral",
      sortOrder: "asc",
      filtros: this.filtros?JSON.stringify(deleteEmptyData(this.filtros)):''
    }
    this.ngxService.start();
    this.service.getAll(params).subscribe(
      (response: any) => {
        if(response){
          this.ngxService.stop();
          this.totalRecords = response.total;
          this.entidad = response.lista;
        }else {
          this.totalRecords = 0;
          this.entidad = [];
        }

      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }

  getMunicipios() {
    this.ngxService.start();
    this.service.getMunicipios().subscribe(
      (response: any) => {
          this.ngxService.stop();
          this.municipios = response;
      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }
  filtrar(){
    this.filtros = {
      nombre: this.form.controls.nombre.value,
      apellido: this.form.controls.apellido.value,
      "cuentaCatastral.idCuentaCatastral":this.form.controls.cuentaCatastral.value,
      "idMunicipio": this.form.controls.municipio.value,
    }
    this.getEntidad();
  }

 /*  ver(id:any){
    this.router.navigate(['/gestion-interna/detalle-cuenta-catastral/'+id+'/visualizar']);
  } */
/*   editar(id:any){
    this.router.navigate(['/gestion-interna/detalle-cuenta-catastral/'+id+'/editar']);
  } */
  ver(id: any){
    //console.log(id);
    const modalRef : any = this.modalService
    .open(CuentaCatastralAgregarComponent,{size:'xl',backdrop:'static'});
    modalRef.componentInstance.titulo='Visualizar';
    modalRef.componentInstance.idCuenta=id;
    modalRef.componentInstance.id=id;
    modalRef.componentInstance.editar=false;
    modalRef.componentInstance.ver=true;
    modalRef.result.then((result:any) => {
      this.getEntidad();
    }, (reason: any) => {
    });
  }
  editar(id: any){
    //console.log('hola'+id);
    const modalRef : any = this.modalService
    .open(CuentaCatastralAgregarComponent,{size:'xl',backdrop:'static'});
    modalRef.componentInstance.titulo='Editar';
    modalRef.componentInstance.idCuenta=id;
    modalRef.componentInstance.id=id;
    modalRef.componentInstance.editar=true;
    modalRef.result.then((result:any) => {
      this.getEntidad();
    }, (reason: any) => {
    });
  }
  eliminarUsuario(item:any) {
    this.ngxService.start();
    if (item.idUsuario != null) {
      let body ={
        nombre: item.nombre,
        apellido: item.apellido,
        correo: item.correo,
        direccion: item.direccion,
        password: item.password,
        rol:{idRol: item.rol},
        telefono: item.telefono,
      }
      this.service.desactivarCuenta(item.idCuentaCatastral).subscribe(
        (result: any) => {
          this.ngxService.stop();
          this.entidad = [];
          this.totalRecords = 0;
          this.notifier.notify('success', result.mensaje);
          this.getEntidad();

        },
        (error: any) => {
          this.ngxService.stop();
          this.notifier.notify('error', error.error.mensaje);
        }

      )
    } else {
      this.messageService.add({ severity: 'warning', summary: '¡Advertencia!', detail: 'Seleccione un registro' });
    }

  }
  eliminar(item:any) {
    this.confirmationService.confirm({
      message: '¿Está seguro que desea eliminar la Cuenta Catastral ?',
      header: 'Confirmar Eliminación',
      icon: 'pi pi-info-circle',
      accept: () => {
        this.eliminarUsuario(item);
      },
      reject: () => {

      }
    });
  }
  paginate(event: any) {
    this.page = event.page +1;
    this.entidad = [];
    this.getEntidad();
  }
  agregar(){
    console.log('acá está mal'+ this.authService.getUserId());
    const modalRef : any = this.modalService
    .open(CuentaCatastralAgregarComponent,{size:'xl',backdrop:'static'});
    modalRef.componentInstance.titulo='Agregar';
    modalRef.componentInstance.agregar=true;
    modalRef.componentInstance.id=this.authService.getUserId();
    modalRef.result.then((result:any) => {

    }, (reason: any) => {
      this.entidad = [];
      this.totalRecords = 0;
      this.getEntidad();
    });
  }
  limpiar(){
    this.page = 1;
    this.filtros = null;
    this.totalRecords = 0;
    this.entidad = [];
    this.form.reset();
    this.paginator.changePage(0);
    this.getEntidad();
  }
}

