import { TokenStorageService } from './../../services/token-storage.service';
import { AuthService } from './../../services/auth.service';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { Mensaje } from 'src/app/config/constantes';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})

export class LoginComponent implements OnInit {
  hide = true;
  isRolAdmin = false;
  isRolCliente = false;
  form: FormGroup;
  submit = false;
  isLoggedIn = false;
  isLoginFailed = false;
  errorMessage = '';
  get f() {
    return this.form.controls;
  }
  constructor(public router: Router, private formBuilder: FormBuilder, private ngxService: NgxUiLoaderService,
    private notifierService: NotifierService, private service: AuthService, private authStorage: TokenStorageService) {
    this.form = this.formBuilder.group(
      {
        correo: [null, Validators.required],
        password: [null, Validators.required],
      }
    );

  }

  ngOnInit(): void {
    if (this.authStorage.getAutenticado()) {
      this.isLoggedIn = true;
    }
  }


  login() {
    this.ngxService.start();
    this.submit = true;
    if (this.form.invalid) {
      this.ngxService.stop();
      this.notifierService.notify('error', Mensaje.VERIFICAR_CAMPOS);
      return;
    }
    let body = {
      correo: this.form.controls.correo.value,
      password: this.form.controls.password.value
    }
    this.service.login(body).subscribe(
      (response:any) => {
        if(response){
          this.ngxService.stop();
          this.isLoginFailed = false;
          this.isLoggedIn = true;
          this.authStorage.setAutenticado(true);
          this.authStorage.saveUser(response.nombre);
          this.authStorage.setRoles(response.codigoRol);
          this.authStorage.setUserId(response.id);
          this.authStorage.setPrimerLogin(response.primerLogin);
          this.reloadPage();
        }
      },
      (response:any) => {
        this.ngxService.stop();
        this.errorMessage = response.mensaje;
        this.isLoginFailed = true;
      }
    );
  }

  reloadPage() {
    this.isRolAdmin = this.authStorage.isRol('ADMIN');
    this.isRolCliente = this.authStorage.isRol('CLIENTE');
    if( this.isRolCliente){
      this.router.navigate(['gestion-interna/cuenta-catastral']);
    }else {
      this.router.navigate(['gestion-interna/cuentas-catastrales']);
    }

  }
}



