import { DeviceDetectorService } from 'ngx-device-detector';
import { DiasRecoleccionService } from './../../services/dias-recoleccion.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { CuentacatastralService } from 'src/app/services/cuentacatastral.service';
import { TokenStorageService } from 'src/app/services/token-storage.service';

@Component({
  selector: 'app-dias-recoleccion',
  templateUrl: './dias-recoleccion.component.html',
  styleUrls: ['./dias-recoleccion.component.scss']
})
export class DiasRecoleccionComponent implements OnInit {
  totalRecords =0;
  entidad:any = [];
  dataRelacion: any = {};
  page: number = 1;
  size: number = 10;
  municipios: any = [];
  isMobile = false;
  form: FormGroup;

   constructor(private service: CuentacatastralService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private deviceService: DeviceDetectorService, private messageService: MessageService, private notifier: NotifierService, private authService: TokenStorageService,
    private formBuilder: FormBuilder, private modalService: NgbModal, private recoleccionService: DiasRecoleccionService) {
      this.isMobile = this.deviceService.isMobile();
      this.form = this.formBuilder.group(
        {
          municipio: [null],
        }
      );
  }

  ngOnInit(): void {
    this.getMunicipios();

  }
  getMunicipios() {
    this.ngxService.start();
    this.service.getMunicipios().subscribe(
      (response: any) => {
          this.ngxService.stop();
          this.municipios = response;
          if( this.municipios.length > 0){
            this.form.patchValue({
              municipio: this.municipios[0].idMunicipio,
            });
            this.listar();
          }
         
      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }

  listar(){
    this.ngxService.start();
    this.recoleccionService.getByIdMunicipio(this.form.controls.municipio.value).subscribe(
      (response: any) => {
          this.ngxService.stop();
          this.entidad = response;
          console.log(response)
      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }

}
