import { PuntoPagoService } from './../../services/punto-pago.service';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { CuentacatastralService } from 'src/app/services/cuentacatastral.service';
import { DiasRecoleccionService } from 'src/app/services/dias-recoleccion.service';
import { TokenStorageService } from 'src/app/services/token-storage.service';
declare var $: any;
declare const google: any;
@Component({
  selector: 'app-puntos-de-pago',
  templateUrl: './puntos-de-pago.component.html',
  styleUrls: ['./puntos-de-pago.component.scss']
})
export class PuntosDePagoComponent implements OnInit {
  municipios: any = [];
  entidad:any = [];
  form: FormGroup;
  overlays: any;
  options: any;
  visible = false;
  map: any =  google.maps.Map;
  dialogVisible:boolean = false;
  selectedPosition: any;
  infoWindow: any;
  markerTitle:any;
  constructor(private service: CuentacatastralService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService, private notifier: NotifierService, private authService: TokenStorageService,
    private formBuilder: FormBuilder,
    private punto: PuntoPagoService, private modalService: NgbModal, private recoleccionService: DiasRecoleccionService) {

      this.form = this.formBuilder.group(
        {
          municipio: [null],
        }
      );

  }

  ngOnInit(): void {
    this.getMunicipios();
    this.options = {
      center: { lat:  -25.313192494788872, lng:-57.55362932047217 },
      zoom: 19
    };
    
    this.infoWindow =new google.maps.InfoWindow();
    this.overlays = [
      new google.maps.Marker({
        position: { lat:  -25.313192494788872, lng:-57.55362932047217 }, title: 'Ecologia Verde S.A. Recolector De Basuras',
        icon: {
          url: 'assets/img/icono-mapa.png',
          scaledSize: new google.maps.Size(20,30),
          origin: new google.maps.Point(0, 0),
          labelOrigin: new google.maps.Point(23, 19)
        },
        draggable: false,
      }),
    ];
    this.markerTitle =null;
    this.dialogVisible =false;
  }
  handleMapClick(event: any){
    this.dialogVisible =true;
    this.selectedPosition =event.latLng;
    }
  handleOverlayClick(event: any){
    let isMarker =event.overlay.getTitle !=undefined;
    if(isMarker){
    let contenido =event.overlay.content;
    console.log(contenido)
    this.infoWindow.setContent('<strong style="font-size: 1rem;color: #22b56b;font-weight: 500;">'+ contenido.nombre +'</strong><br>'+
    '<li style="font-size: 0.9rem;font-weight: 400;"> Tel:' + contenido.telefono +'<br></li>' +  
    '<li style="font-size: 0.9rem;font-weight: 400;"> Dirección:'+ contenido.direccion +'<br></li>' +'<li style="font-size: 0.9rem;font-weight: 400;"> Municipio:' + contenido.municipio.nombre +'<br></li>');
    this.infoWindow.open(event.map,event.overlay);
    event.map.setCenter(event.overlay.getPosition());
    }
    }
  getMunicipios() {
    this.ngxService.start();
    this.service.getMunicipios().subscribe(
      (response: any) => {
          this.ngxService.stop();
          this.municipios = response;
          if( this.municipios.length > 0){
            this.form.patchValue({
              municipio: this.municipios[0].idMunicipio,
            });
            this.listar();
          }
      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }

  listar(){
   this.ngxService.start();
    this.punto.getByIdMunicipio(this.form.controls.municipio.value).subscribe(
      (response: any) => {
          this.ngxService.stop();
          this.entidad = response;
          this.ubicarMarker();
          console.log(response)
      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )

  }
  ubicarMarker(){
    this.overlays = [];
    for (const iterator of this.entidad) {
      this.options = {
        center: { lat:  -25.313192494788872, lng:-57.55362932047217 },
        zoom: 8
      };
     
      this.overlays.push(new google.maps.Marker({position: {lat: iterator.latitud, lng: iterator.longitud}, title: iterator.nombre,
        content: iterator,   
        icon: {
        url: 'assets/img/icono-mapa.png',
        scaledSize: new google.maps.Size(20,30),
        origin: new google.maps.Point(0, 0),
        labelOrigin: new google.maps.Point(23, 19)
        }
      },
      ),
      )
    }
    const contentString =
    '<div id="content">' +
    '<div id="siteNotice">' +
    "</div>" +
    '<h1 id="firstHeading" class="firstHeading">Uluru</h1>' +
    '<div id="bodyContent">' +
    "<p><b>Uluru</b>, also referred to as <b>Ayers Rock</b>, is a large " +
    "sandstone rock formation in the southern part of the " +
    "Northern Territory, central Australia. It lies 335&#160;km (208&#160;mi) " +
    "south west of the nearest large town, Alice Springs; 450&#160;km " +
    "(280&#160;mi) by road. Kata Tjuta and Uluru are the two major " +
    "features of the Uluru - Kata Tjuta National Park. Uluru is " +
    "sacred to the Pitjantjatjara and Yankunytjatjara, the " +
    "Aboriginal people of the area. It has many springs, waterholes, " +
    "rock caves and ancient paintings. Uluru is listed as a World " +
    "Heritage Site.</p>" +
    '<p>Attribution: Uluru, <a href="https://en.wikipedia.org/w/index.php?title=Uluru&oldid=297882194">' +
    "https://en.wikipedia.org/w/index.php?title=Uluru</a> " +
    "(last visited June 22, 2009).</p>" +
    "</div>" +
    "</div>";

  

    
    const infowindow = new google.maps.InfoWindow({
      content: contentString,
    });

  }
}
