import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { Mensaje } from 'src/app/config/constantes';
import { UsuariosService } from 'src/app/services/usuarios.service';
import * as $ from 'jquery';
@Component({
  selector: 'app-form-usuario',
  templateUrl: './form-usuario.component.html',
  styleUrls: ['./form-usuario.component.scss']
})
export class FormUsuarioComponent implements OnInit {
  roles: any[] = [];
  titulo: any;
  id: any;
  form: FormGroup;
  activatedView = false;
  activatedEdit = false;
  submit = false;
  tituloBoton = 'Guardar';
  get f() {
    return this.form.controls;
  }
  constructor(private service: UsuariosService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService, private notifier: NotifierService,
    private formBuilder: FormBuilder, private route: ActivatedRoute) {
      this.activatedView = this.router.url.includes('/visualizar');
      this.activatedEdit = this.router.url.includes('/editar');
      this.form = this.formBuilder.group(
        {
          nombre: [{value: null, disabled: this.activatedView}, Validators.required],
          apellido: [{value: null, disabled: this.activatedView}, Validators.required],
          correo: [{value: null, disabled: this.activatedView},  Validators.required, Validators.pattern("^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$")],
          direccion: [{value: null, disabled: this.activatedView}, Validators.required],
          password: [{value: null, disabled: this.activatedView}, Validators.required],
          rol: [{value: null, disabled: this.activatedView}, Validators.required],
          telefono: [{value: null, disabled: this.activatedView}],
        }
      );

  }


  ngOnInit(): void {
    this.getRoles();
    this.titulo = 'Agregar';
    this.id = this.route.snapshot.paramMap.get('id');
    this.ngxService.stop();
    if (this.id != null){
        this.ngxService.stop();
        if (this.activatedView){
          this.titulo = 'Ver';
        }else{
          this.tituloBoton = 'Actualizar';
          this.titulo = 'Editar';
        }
        this.service.getById(parseInt(this.id)).subscribe(
          (response: any) =>  {
            this.ngxService.stop();
            this.form.patchValue({
              nombre: response.nombre,
              apellido: response.apellido,
              correo: response.correo,
              direccion: response.direccion,
              password: response.password,
              rol: response.rol.idRol,
              telefono: response.telefono
            });
          },
          (error: any) => {
            this.ngxService.stop();
            this.notifier.notify('error', error.error.mensaje);
          }
      );
    }
  }
  getRoles() {
    this.ngxService.start();
    this.service.getRoles().subscribe(
      (response: any) => {
          this.ngxService.stop();
          this.roles = response;
      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }
  guardar(){
    this.submit = true;
    if (this.form.invalid) {
      this.notifier.notify('error', Mensaje.VERIFICAR_CAMPOS);
      return;
    } else{
      if(!this.activatedEdit){
        this.ngxService.start();
        let body = {
          nombre: this.form.controls.nombre.value,
          apellido: this.form.controls.apellido.value,
          correo: this.form.controls.correo.value,
          direccion: this.form.controls.direccion.value,
          password: this.form.controls.password.value,
          rol:{idRol: this.form.controls.rol.value},
          telefono: this.form.controls.telefono.value,
        }
        this.service.save(body).subscribe(
          (response: any) => {
            if(!response.exitoso){
              this.ngxService.stop();
              this.notifier.notify('error', response.mensaje);
             } else {
              this.ngxService.stop();
              this.notifier.notify('success', response.mensaje);
              this.router.navigate(['/gestion-interna/usuarios']);
             }

          },
          (response: any) => {
            this.ngxService.stop();
            
          }

        )
      } else {
        this.ngxService.start();
        let body = {
          nombre: this.form.controls.nombre.value,
          apellido: this.form.controls.apellido.value,
          correo: this.form.controls.correo.value,
          direccion: this.form.controls.direccion.value,
          rol:{idRol: this.form.controls.rol.value},
          /* password: this.form.controls.password.value, */
          telefono: this.form.controls.telefono.value,
        }
        this.service.update(this.id, body).subscribe(
          (response: any) => {
             if(!response.exitoso){
              this.ngxService.stop();
              this.notifier.notify('error', response.mensaje);
             } else {
              this.ngxService.stop();
              this.notifier.notify('success', response.mensaje);
              this.router.navigate(['/gestion-interna/usuarios']);
             }
          },
          (response: any) => {
            this.ngxService.stop();
            
          }

        )
      }
    }

  }
}
