import { DiasRecoleccionService } from 'src/app/services/dias-recoleccion.service';
import { CuentacatastralService } from '../../../services/cuentacatastral.service';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { Mensaje } from 'src/app/config/constantes';
import { UsuariosService } from 'src/app/services/usuarios.service';

@Component({
  selector: 'app-dias-recoleccion-form',
  templateUrl: './dias-recoleccion-form.component.html',
  styleUrls: ['./dias-recoleccion-form.component.scss']
})
export class DiasRecoleccionFormComponent implements OnInit {

  municipios: any[] = [];
  dias= [{id:'lunes', nombre: 'Lunes'},{id:'martes', nombre: 'Martes'},{id:'miercoles', nombre: 'Miércoles'},
  {id:'jueves', nombre: 'Jueves'},{id:'viernes', nombre: 'Viernes'},{id:'sabado', nombre: 'Sábado'},{id:'domingo', nombre: 'Domingo'}]
  titulo: any;
  id: any;
  form: FormGroup;
  activatedView = false;
  activatedEdit = false;
  submit = false;
  tituloBoton = 'Guardar';
  get f() {
    return this.form.controls;
  }
  constructor(private service: DiasRecoleccionService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService, private notifier: NotifierService,
    private formBuilder: FormBuilder, private route: ActivatedRoute, private cuentaService: CuentacatastralService) {
      this.activatedView = this.router.url.includes('/visualizar');
      this.activatedEdit = this.router.url.includes('/editar');
      this.form = this.formBuilder.group(
        {
          dia: [{value: null, disabled: this.activatedView}, Validators.required],
          barrio: [{value: null, disabled: this.activatedView}, Validators.required],
          municipio: [{value: null, disabled: this.activatedView}, Validators.required],
        }
      );

  }


  ngOnInit(): void {
    this.getmunicipios();
    this.titulo = 'Agregar';
    this.id = this.route.snapshot.paramMap.get('id');
    this.ngxService.stop();
    if (this.id != null){
        this.ngxService.stop();
        if (this.activatedView){
          this.titulo = 'Ver';
        }else{
          this.tituloBoton = 'Actualizar';
          this.titulo = 'Editar';
        }
        this.service.getById(parseInt(this.id)).subscribe(
          (response: any) =>  {
            this.ngxService.stop();
            this.form.patchValue({
              dia: response.dia,
              barrio: response.barrio,
              municipio: response.municipio.idMunicipio
            });
          },
          (error: any) => {
            this.ngxService.stop();
            this.notifier.notify('error', error.error.mensaje);
          }
      );
    }
  }
  getmunicipios() {
    this.ngxService.start();
    this.cuentaService.getMunicipios().subscribe(
      (response: any) => {
          this.ngxService.stop();
          this.municipios = response;
      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }
  guardar(){
    this.submit = true;
    if (this.form.invalid) {
      this.notifier.notify('error', Mensaje.VERIFICAR_CAMPOS);
      return;
    } else{
      if(!this.activatedEdit){
        this.ngxService.start();
        let body = {
          dia: this.form.controls.dia.value,
          barrio: this.form.controls.barrio.value,
          municipio:{
            idMunicipio:this.form.controls.municipio.value
         }
       }

        this.service.save(body).subscribe(
          (response: any) => {
            if(!response.exitoso){
              this.ngxService.stop();
              this.notifier.notify('error', response.mensaje);
             } else {
              this.ngxService.stop();
              this.notifier.notify('success', response.mensaje);
              this.router.navigate(['/gestion-interna/dias-recoleccion']);
             }

          },
          (response: any) => {
            this.ngxService.stop();
            
          }

        )
      } else {
        this.ngxService.start();
        let body = {
          dia: this.form.controls.dia.value,
          barrio: this.form.controls.barrio.value,
          municipio:{
          idMunicipio:this.form.controls.municipio.value
         }
        }
        this.service.update(this.id, body).subscribe(
          (response: any) => {
             if(!response.exitoso){
              this.ngxService.stop();
              this.notifier.notify('error', response.mensaje);
             } else {
              this.ngxService.stop();
              this.notifier.notify('success', response.mensaje);
              this.router.navigate(['/gestion-interna/dias-recoleccion']);
             }
          },
          (response: any) => {
            this.ngxService.stop();
            
          }

        )
      }
    }

  }

}
