import { TokenStorageService } from './../../services/token-storage.service';
import { Component, OnInit } from '@angular/core';
import { DeviceDetectorService } from 'ngx-device-detector';
import { AuthService } from 'src/app/services/auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss']
})
export class MainComponent implements OnInit {
  isMobile = false;
  nombreUsuario: any | null;
  constructor(private loginService: AuthService, private deviceService: DeviceDetectorService,
     private authService: TokenStorageService,public router: Router) {
    this.isMobile = this.deviceService.isMobile();
  }

  ngOnInit(): void {
    this.nombreUsuario = this.authService.getUser() != null ? this.authService.getUser():'';
  }
  cerrarSesion(){
    this.loginService.logout().subscribe( (r:any) =>{
      window.localStorage.clear();
      this.router.navigate(['/login']);
    });
  }
}
