import { DiasRecoleccionFormComponent } from './page/administrador/dias-recoleccion-form/dias-recoleccion-form.component';
import { ContactoComponent } from './web-publica/contacto/contacto.component';
import { PuntosDePagoComponent } from './web-publica/puntos-de-pago/puntos-de-pago.component';
import { NosotrosComponent } from './web-publica/nosotros/nosotros.component';
import { WebPublicaPrincipalComponent } from './web-publica/web-publica-principal/web-publica-principal.component';
import { InicioComponent } from './web-publica/inicio/inicio.component';
import { NotificacionesComponent } from './page/administrador/notificaciones/notificaciones.component';
import { ReclamosComponent } from './page/administrador/reclamos/reclamos.component';
import { PuntosPagoComponent } from './page/administrador/puntos-pago/puntos-pago.component';
import { DiasRecoleccionComponent } from './web-publica/dias-recoleccion/dias-recoleccion.component';
import { CuentaCatastralComponent } from './page/administrador/cuenta-catastral/cuenta-catastral.component';
import { ConfirmarEmailComponent } from './seguridad/confirmar-email/confirmar-email.component';
import { RestablecerContrasenhaComponent } from './seguridad/restablecer-contrasenha/restablecer-contrasenha.component';
import { MainComponent } from './dashboard/main/main.component';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomeComponent } from './page/home/home.component';
import { LoginComponent } from './seguridad/login/login.component';
import { DiasdeRecoleccionComponent } from './page/administrador/dias-recoleccion/dias-de-recoleccion.component';
import { DetalleReclamoComponent } from './page/administrador/detalle-reclamo/detalle-reclamo.component';
import { AtenderReclamoComponent } from './page/administrador/atender-reclamo/atender-reclamo.component';
import {DetalleCuentaCatastralComponent} from "./page/administrador/detalle-cuenta-catastral/detalle-cuenta-catastral.component";
import {SubZonaComponent} from "./page/sub-zona/sub-zona.component";
import { ListaNotificacionesComponent } from './page/administrador/notificaciones/lista-notificaciones/lista-notificaciones.component';
import { DetalleNotificacionComponent } from './page/detalle-notificacion/detalle-notificacion.component';
import { UnauthorizedComponent } from './seguridad/unauthorized/unauthorized.component';
import { RestrictAdministradorComponent } from './seguridad/permiso-pantalla/restrict-administrador.component';
import { RestrictClienteComponent } from './seguridad/permiso-pantalla/restrict-cliente.component';
import { BannerListadoComponent } from './page/administrador/gestion-banner/banner-listado/banner-listado.component';
import { FormUsuarioComponent } from './page/administrador/gestion-usuarios/form-usuario/form-usuario.component';
import { ListarUsuariosComponent } from './page/administrador/gestion-usuarios/listar-usuarios/listar-usuarios.component';
import { FormMunicipioComponent } from './page/administrador/municipios/form-municipio/form-municipio.component';
import { ListarMunicipioComponent } from './page/administrador/municipios/listar-municipio/listar-municipio.component';
import { PuntoPagoFormComponent } from './page/administrador/punto-pago-form/punto-pago-form.component';
import { CuentaCatastralPerfilComponent } from './page/clientes/cuenta-catastral-perfil/cuenta-catastral-perfil.component';
import { NotificacionClienteComponent } from './page/clientes/notificacion-cliente/notificacion-cliente.component';
import { ReclamoClienteComponent } from './page/clientes/reclamo-cliente/reclamo-cliente.component';
import { CuentaCatastralFacturacionComponent } from './page/clientes/cuenta-catastral-facturacion/cuenta-catastral-facturacion.component';

const routes: Routes = [
  {path: '',redirectTo: 'portal-publico',pathMatch:'full' },
  {path: 'login', component: LoginComponent},
  {path: 'restablecer-contrasenha', component: RestablecerContrasenhaComponent},
  {path: 'enviar-correo-restablecimiento', component: ConfirmarEmailComponent},
  {path: 'portal-publico', component: WebPublicaPrincipalComponent,
  children: [
    { path: 'inicio', component: InicioComponent},
    { path: 'nosotros', component: NosotrosComponent},
    { path: 'dias-de-recoleccion', component: DiasRecoleccionComponent},
    { path: 'puntos-de-pagos', component: PuntosDePagoComponent},
    { path: 'contactanos', component: ContactoComponent},
    { path: '',redirectTo: 'inicio',pathMatch:'full' },
  ]},
  {path: 'gestion-interna', component: MainComponent, 
    children: [
      /* administrador */

      { path: 'unauthorized', component: UnauthorizedComponent},
      { path: 'restablecer-contrasenha', component: RestablecerContrasenhaComponent, canActivate: [RestrictAdministradorComponent]},
      { path: 'enviar-correo-restablecimiento', component: ConfirmarEmailComponent, canActivate: [RestrictAdministradorComponent]},

      { path: 'usuarios', component: ListarUsuariosComponent , canActivate: [RestrictAdministradorComponent]},
      { path: 'usuario/editar/:id', component: FormUsuarioComponent , canActivate: [RestrictAdministradorComponent]},
      { path: 'usuario/visualizar/:id', component: FormUsuarioComponent, canActivate: [RestrictAdministradorComponent] },
      { path: 'usuario/agregar', component: FormUsuarioComponent , canActivate: [RestrictAdministradorComponent]},

      { path: 'cuentas-catastrales', component: CuentaCatastralComponent, canActivate: [RestrictAdministradorComponent] },

      { path: 'municipios', component: ListarMunicipioComponent , canActivate: [RestrictAdministradorComponent]},
      { path: 'municipio/agregar', component: FormMunicipioComponent , canActivate: [RestrictAdministradorComponent]},
      { path: 'municipio/editar/:id', component: FormMunicipioComponent, canActivate: [RestrictAdministradorComponent] },
      { path: 'municipio/visualizar/:id', component: FormMunicipioComponent, canActivate: [RestrictAdministradorComponent] },


      { path: 'dias-recoleccion', component: DiasdeRecoleccionComponent, canActivate: [RestrictAdministradorComponent] },
      { path: 'dia-recoleccion/agregar', component: DiasRecoleccionFormComponent, canActivate: [RestrictAdministradorComponent] },
      { path: 'dia-recoleccion/editar/:id', component: DiasRecoleccionFormComponent, canActivate: [RestrictAdministradorComponent] },
      { path: 'dia-recoleccion/visualizar/:id', component: DiasRecoleccionFormComponent, canActivate: [RestrictAdministradorComponent] },

      { path: 'puntos-de-pago', component: PuntosPagoComponent, canActivate: [RestrictAdministradorComponent] },
      { path: 'punto-de-pago/agregar', component: PuntoPagoFormComponent, canActivate: [RestrictAdministradorComponent] },
      { path: 'punto-de-pago/editar/:id', component: PuntoPagoFormComponent , canActivate: [RestrictAdministradorComponent]},
      { path: 'punto-de-pago/visualizar/:id', component: PuntoPagoFormComponent, canActivate: [RestrictAdministradorComponent] },

     
      { path: 'notificaciones', component: ListaNotificacionesComponent , canActivate: [RestrictAdministradorComponent]},
      { path: 'notificacion/agregar', component: NotificacionesComponent, canActivate: [RestrictAdministradorComponent] },

      { path: 'notificacion/editar/:id', component: NotificacionesComponent, canActivate: [RestrictAdministradorComponent]},
      { path: 'notificacion/visualizar/:id', component: NotificacionesComponent, canActivate: [RestrictAdministradorComponent]},

      { path: 'reclamos', component: ReclamosComponent , canActivate: [RestrictAdministradorComponent]},

      { path: 'banners', component: BannerListadoComponent, canActivate: [RestrictAdministradorComponent] },

      { path: 'sub-zonas', component: SubZonaComponent, canActivate: [RestrictAdministradorComponent] },

      { path: 'reclamo/visualizar/:id', component:   DetalleReclamoComponent, canActivate: [RestrictAdministradorComponent] },
      { path: 'reclamo/atender/:id', component:   AtenderReclamoComponent, canActivate: [RestrictAdministradorComponent] },
      { path: 'detalle-cuenta-catastral/:id/editar', component:   DetalleCuentaCatastralComponent, canActivate: [RestrictAdministradorComponent] },
      { path: 'detalle-cuenta-catastral/:id/visualizar', component:   DetalleCuentaCatastralComponent, canActivate: [RestrictAdministradorComponent] },
      
      /* cliente */
      
      { path: 'cuenta-catastral', component: CuentaCatastralFacturacionComponent, canActivate: [RestrictClienteComponent] },
      { path: 'cuenta-catastral/perfil', component: CuentaCatastralPerfilComponent, canActivate: [RestrictClienteComponent] },

      { path: 'notificacion', component:   NotificacionClienteComponent, canActivate: [RestrictClienteComponent] },
      { path: 'realizar-reclamo', component:   ReclamoClienteComponent, canActivate: [RestrictClienteComponent] },

      { path: 'notificacion/visualizar/:id', component: DetalleNotificacionComponent, canActivate: [RestrictClienteComponent] },
     





   

    
      
     


    ]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
