import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Servers } from '../config/api';
const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};
@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private loginUrl = Servers.ECOLOGIA.baseUrl + 'auth';
  private url = Servers.ECOLOGIA.baseUrl;
  constructor(private http: HttpClient) { }

  login(body: any): Observable<any> {
    return this.http.post(this.loginUrl,body, httpOptions);
  }
  enviarCorreo(body: any): Observable<any> {
    return this.http.post(this.url + 'cambio-contrasenha',body, httpOptions);
  }
  logout(){
    return this.http.post(this.loginUrl + '/cerrar', httpOptions);
  }
  public isManager(): boolean {
    let isManager = false;
    let user = this.getUserToken();
    return isManager;
  }

  public getUserToken(): any {
      return localStorage.getItem('jwtTokenName');
  }

  getPosition(): Promise<any> {
    return new Promise((resolve, reject) => {
        navigator.geolocation.getCurrentPosition(resp => {
                resolve({lng: resp.coords.longitude, lat: resp.coords.latitude});
            },
            err => {
                reject(err);
          });
    });
  }
  resetPass(body: any): Observable<any> {
    return this.http.put(Servers.ECOLOGIA.baseUrl + 'cambio-contrasenha/reestablecerpass/', body);
  }



}
