import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NotifierService } from 'angular-notifier';
import { debug } from 'console';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { deleteEmptyData } from 'src/app/config/app-utils';
import { CuentacatastralService } from 'src/app/services/cuentacatastral.service';
import { DiasRecoleccionService } from 'src/app/services/dias-recoleccion.service';
import { NotificacionesService } from 'src/app/services/notificaciones.service';
import { TokenStorageService } from 'src/app/services/token-storage.service';

@Component({
  selector: 'app-notificacion-cliente',
  templateUrl: './notificacion-cliente.component.html',
  styleUrls: ['./notificacion-cliente.component.scss']
})
export class NotificacionClienteComponent implements OnInit {

  municipios = [];
  data: any = {};
  form: FormGroup;
  totalRecords =0;
  entidad:any = [];
  dataRelacion: any = {};
  page: number = 1;
  size: number = 10;
  filtros: any = null;
  dias= [{id:'lunes', nombre: 'Lunes'},{id:'martes', nombre: 'Martes'},{id:'miercoles', nombre: 'Miércoles'},
  {id:'jueves', nombre: 'Jueves'},{id:'viernes', nombre: 'Viernes'},{id:'sabado', nombre: 'Sábado'},{id:'domingo', nombre: 'Domingo'}]
   constructor(private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService, private notifier: NotifierService, private authService: TokenStorageService,
    private formBuilder: FormBuilder, private modalService: NgbModal, private service: NotificacionesService) {

      this.form = this.formBuilder.group(
        {
          subZona:[null],
          area: [null],
          descripcion: [null],
          hora:[null],
          contenido:[null],
          inicioVigencia:[null],
          finVigencia:[null],
        }
      );
  }

  ngOnInit(): void {
    
    this.filtrar();
  }
  getEntidad() {
   
    const params = {
      cantidad: 10,
      pagina: this.page,
      sortBy:"idNotificacion",
      sortOrder: "desc",
      filtros: this.filtros === null ? '' : JSON.stringify(deleteEmptyData(this.filtros))
    }
    this.ngxService.start();
    this.service.getCliente(params).subscribe(
      (response: any) => {
        this.ngxService.stop();
        if(response.exitoso){
       
          this.totalRecords = response.data.total;
          this.entidad = response.data.lista;
        }else {
          this.totalRecords = 0;
          this.entidad = [];
          this.notifier.notify('error', response.mensaje);
        }

      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }
  filtrar(){
    var usuario: any = 0;
    if( localStorage.getItem("userId") != undefined &&  localStorage.getItem("userId") != null){
      usuario = localStorage.getItem("userId");
      usuario = parseInt(usuario);
    } else {
      usuario = null;
    }
    this.filtros = {
      subZona: this.form.controls.subZona.value,
      area: this.form.controls.area.value,
      descripcion: this.form.controls.descripcion.value,
      hora:this.form.controls.hora.value,
      contenido:this.form.controls.contenido.value,
      inicioVigencia:this.form.controls.inicioVigencia.value,
      finVigencia:this.form.controls.finVigencia.value,
      "usuario.idUsuario": usuario
    }
    this.getEntidad();

  }

  limpiar(){
    this.filtros = null;
    this.totalRecords = 0;
    this.entidad = [];
    this.form.reset();
    this.getEntidad();
  }

  paginate(event: any) {
    this.page = event.page +1;
    this.entidad = [];
    this.getEntidad();
  }

  ver(id:any){
   /*  console.log('id que mando', id)
    const modalRef : any = this.modalService
    .open(DetalleNotificacionComponent,{size:'lg',backdrop:'static'});
    modalRef.componentInstance.id=id; */
    this.router.navigate(['/gestion-interna/notificacion/visualizar/'+id]);
  }
}
