import { Injectable } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor, HttpResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import {catchError, map} from 'rxjs/operators';
import {Router} from '@angular/router';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { NotifierService } from 'angular-notifier';
import { Mensaje } from '../config/constantes';
@Injectable()
export class ErrorInterceptor implements HttpInterceptor {
  constructor( private notifier: NotifierService,private ngxService: NgxUiLoaderService, private router: Router) {}
  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    return next.handle(request).pipe(
      map((response: any) => {
        if (response.ok && response.status === 200){
          if (!response.body){
            return throwError(response);
          }
        }
        return response;
      }),
      catchError(err => {
      let error = err.error;
      switch (err.error.status) {
        case 401:
          if(err.statusText === 'Unauthorized'){
            err.statusText ='No Autorizado.';
            err.error.error = 'No Autorizado.';
            err.error.message = 'No Autorizado.';
          }
          window.localStorage.clear();
          error = err.error;
          this.notifier.notify('error', err.error.message);
          this.router.navigate(['login']);
          break;
        case 400:
          this.notifier.notify('error', err.error.message);
          break;
        case 500:
          this.notifier.notify('error', err.error.message);
        break;
        case 403:
          this.notifier.notify('error', err.error.message);
        break;
        case 404:
          this.notifier.notify('error', err.error.message);
        break;
        case 404:
          this.notifier.notify('error', err.error.message);
        break;
        default:
          this.notifier.notify('error', Mensaje.ERROR_404);
          break;
      }
      return throwError(error);
    }));
  }
}
