import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { ElementRef } from '@angular/core';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { Mensaje } from 'src/app/config/constantes';
import { CuentacatastralService } from 'src/app/services/cuentacatastral.service';
import { ReclamosService } from 'src/app/services/reclamos.service';
import { TokenStorageService } from 'src/app/services/token-storage.service';
declare var $: any;
declare const google: any;
@Component({
  selector: 'app-reclamo-cliente',
  templateUrl: './reclamo-cliente.component.html',
  styleUrls: ['./reclamo-cliente.component.scss']
})
export class ReclamoClienteComponent implements OnInit {
  overlays: any;
  options: any;
  map: any =  google.maps.Map;
  es: any = [];
  idArchivos: any =  [];
  form: FormGroup;
  fileName = null;
  fileType : any = null;
  fileInput!: ElementRef;
  submit = false;
  archivos: any =  [];
  base64 : any =  null;
  get f() {
    return this.form.controls;
  }
   constructor(private service: ReclamosService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService, private notifier: NotifierService, private authService: TokenStorageService,
    private formBuilder: FormBuilder, private modalService: NgbModal) {

      this.form = this.formBuilder.group(
        {
          descripcion: [null, Validators.required],
          latitud: [{value: null, disabled: true}],
          longitud:[{value: null, disabled: true}]
        }
      );
  }

  ngOnInit(): void {
    this.options = {
      center: { lat:  -25.313192494788872, lng:-57.55362932047217 },
      zoom: 19
    };
    this.overlays = [
      new google.maps.Marker({
        position: { lat:  -25.313192494788872, lng:-57.55362932047217 }, title: 'Ecologia Verde S.A. Recolector De Basuras',
        icon: {
          url: 'assets/img/icono-eco.png',
          scaledSize: new google.maps.Size(35, 50),
          origin: new google.maps.Point(0, 0),
          labelOrigin: new google.maps.Point(23, 19)
        },
        draggable: true
      }),
    ];
    this.overlays[0].addListener('dragend', (function (notifier) {
      return function () {
      //  notifier.notify('success', Mensaje.UBICACION_ACTUALIZADA);
      };
    })(this.notifier));
  }
  handleMapClick(event:any) {
    this.form.patchValue({
      latitud:this.overlays[0].getPosition().lat(),
      longitud:this.overlays[0].getPosition().lng(),
    });
  }
  guardar(){
    this.submit = true;
    var index = this.archivos.length;
    var i = 0;
    if (this.form.invalid) {
      this.notifier.notify('error', Mensaje.VERIFICAR_CAMPOS);
      return;
    } else{
      const promise =
          new Promise<void>((resolve, reject) => {
            if( this.archivos.length > 0){
              for (const iterator of this.archivos) {
                //conv a base 64
                let body ={
                  archivo: iterator.base64,
                  nombreArchivo: iterator.name
                };
                this.ngxService.start();
                this.service.agregarAdjunto(JSON.stringify(body)).subscribe(
                  (response: any) => {
                    i++;
                    if(!response.exitoso){
                      this.notifier.notify('error', response.mensaje);
                      this.ngxService.stop();
                      
                      } else {
                        this.idArchivos.push(response.data.idAdjunto)
                        this.ngxService.stop();
                       // this.notifier.notify('success', response.mensaje);
                      }
                      if(index === i){
                        resolve();
                      }
  
                  },
                  (response: any) => {
                    i++;
                    if(index === i){
                      resolve();
                    }
                    this.ngxService.stop();
                    
                  }
  
                )
              }
            } else {
              resolve();
            }
           
          })
          .then(
          () => {
            let body = {
              descripcion: this.form.controls.descripcion.value,
              idArchivos: this.idArchivos,
              latitud: this.form.controls.latitud.value,
              longitud: this.form.controls.longitud.value,
            }
              this.service.agregar(JSON.stringify(body)).subscribe(
                (response: any) => {
                  if(!response.exitoso){
                    this.ngxService.stop();
                    this.notifier.notify('error', response.mensaje);

                    } else {

                      this.ngxService.stop();
                      this.notifier.notify('success', response.mensaje);
                      this.limpiarForm();
                    }
      
                },
                (response: any) => {
                  this.ngxService.stop();
                  
                }
              ); 
          },
          (err) => { console.log(err); }
          );
    }
      



    
  }
  limpiarForm(){
    this.ngxService.start();
    this.form.reset();
    this.archivos = [];
    this.ubicarMarker();
    this.ngxService.stop();
  }
  ubicarMarker(){
    this.overlays = [
      new google.maps.Marker({
        position:{ lat:  -25.313192494788872, lng:-57.55362932047217 }, title: 'Ubicación',
        icon: {
          url: 'assets/img/icono-mapa.png',
          scaledSize: new google.maps.Size(35, 50),
          origin: new google.maps.Point(0, 0),
          labelOrigin: new google.maps.Point(23, 19)
        },
        draggable: true

      }),
    ];
  }
  eliminar(item: any){
    this.ngxService.start();
    const elemento = this.archivos.indexOf(item);
    this.archivos.splice(elemento, 1);
    this.ngxService.stop();
  }
  subirImagen(e: any){

    this.ngxService.start();
    let files = e.files;
    this.fileName = null;
    console.log(e.files);
    if(e.files.length > 0){
      for (const iterator of e.files) {
        this.ngxService.start();
      //  this.archivos.push(iterator);
        this.convertirBase64(iterator);
        
      }
    } else if (files.name === null){
      this.ngxService.stop();
      this.fileInput.nativeElement.value = '';
    }
  }

  convertirBase64(archivo: any) {
    // se codifica el archivo a base 64
  /*   const reader = new FileReader();
    reader.onload = this._handleReaderLoaded.bind(this,archivo);
    reader.readAsBinaryString(archivo); */
    let me = this;
    let reader = new FileReader();
    reader.readAsDataURL(archivo);
    reader.onload = function () {
      me.ngxService.stop();
      archivo['base64'] =  reader.result;
      me.archivos.push(archivo);
      console.log(reader.result);
    };
  }

  _handleReaderLoaded(readerEvt : any, archivo: any) {
    const binaryString = readerEvt.target.result;
    archivo['base64'] =  btoa(binaryString);
    
    btoa(binaryString);
    this.archivos.push(archivo);
  }

  
}
