import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { Paginator } from 'primeng/paginator';
import { deleteEmptyData } from 'src/app/config/app-utils';
import { CuentacatastralService } from 'src/app/services/cuentacatastral.service';
import { NotificacionesService } from 'src/app/services/notificaciones.service';
import { TokenStorageService } from 'src/app/services/token-storage.service';
import { DetalleNotificacionComponent } from '../../../detalle-notificacion/detalle-notificacion.component';

@Component({
  selector: 'app-lista-notificaciones',
  templateUrl: './lista-notificaciones.component.html',
  styleUrls: ['./lista-notificaciones.component.scss']
})
export class ListaNotificacionesComponent implements OnInit {
  @ViewChild('p', {static: false}) paginator: Paginator | any;
  es: any = [];
  municipios = [];
  data: any = {};
  form: FormGroup;
  totalRecords =0;
  entidad:any = [];
  dataRelacion: any = {};
  page: number = 1;
  size: number = 10;
  filtros: any = null;
  dias= [{id:'lunes', nombre: 'Lunes'},{id:'martes', nombre: 'Martes'},{id:'miercoles', nombre: 'Miércoles'},
  {id:'jueves', nombre: 'Jueves'},{id:'viernes', nombre: 'Viernes'},{id:'sabado', nombre: 'Sábado'},{id:'domingo', nombre: 'Domingo'}]
   constructor(private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService, private serviceMunicipio: CuentacatastralService,
    public router: Router, private messageService: MessageService, private notifier: NotifierService, private authService: TokenStorageService,
    private formBuilder: FormBuilder, private modalService: NgbModal, private service: NotificacionesService) {

      this.form = this.formBuilder.group(
        {
          subZona:[null],
          area: [null],
          descripcion: [null],
          hora:[null],
          contenido:[null],
          inicioVigencia:[null],
          finVigencia:[null],
          municipio:[null],
        }
      );
  }

  ngOnInit(): void {
    this.inicializarDatePicker();
    this.getMunicipios();
    this.getEntidad();
  }
  getMunicipios() {
    this.ngxService.start();
    this.serviceMunicipio.getMunicipios().subscribe(
      (response: any) => {
          this.ngxService.stop();
          this.municipios = response;
      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }
  getEntidad() {
    const params = {
      cantidad: 10,
      pagina: this.page,
      sortBy:"inicioVigencia",
      sortOrder: "desc",
      filtros: this.filtros === null ? '' : JSON.stringify(deleteEmptyData(this.filtros))
    }
    this.ngxService.start();
    this.service.get(params).subscribe(
      (response: any) => {
        if(response){
          this.ngxService.stop();
          this.totalRecords = response.total;
          this.entidad = response.lista;
        }else {
          this.totalRecords = 0;
          this.entidad = [];
        }

      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }
  filtrar(){
    this.filtros = {
      subZona: this.form.controls.subZona.value,
      area: this.form.controls.area.value,
      descripcion: this.form.controls.descripcion.value,
      hora:this.form.controls.hora.value,
      contenido:this.form.controls.contenido.value,
      inicioVigencia:this.parseFecha(this.form.controls.inicioVigencia.value),
      finVigencia: this.parseFecha(this.form.controls.finVigencia.value),
      "municipio.idMunicipio": this.form.controls.municipio.value,
    }
    this.getEntidad();

  }
  parseFecha(valor:any) {

    if(valor === undefined || valor === null || valor === ''){
      return valor;
    }else {
      if( JSON.stringify(valor).includes('/')){
        var fechaList = valor.split('/');
        return fechaList[2]+'-'+fechaList[1]+'-'+fechaList[0];
      }else {
        var event = new Date(valor);
        let fecha = JSON.stringify(event);
        fecha = fecha.slice(1,11);

        return fecha;
      }

      

    }
  }
  limpiar(){
    this.page = 1;
    this.filtros = null;
    this.totalRecords = 0;
    this.entidad = [];
    this.form.reset();
    this.paginator.changePage(0);
    this.getEntidad();
  }

  paginate(event: any) {
    this.page = event.page +1;
    this.entidad = [];
    this.getEntidad();
  }

  ver(id:any){
    this.router.navigate(['/gestion-interna/notificacion/visualizar/'+id]);
  }
  inicializarDatePicker(){
    this.es = {
      firstDayOfWeek: 1,
      dayNames: [ "domingo","lunes","martes","miércoles","jueves","viernes","sábado" ],
      dayNamesShort: [ "dom","lun","mar","mié","jue","vie","sáb" ],
      dayNamesMin: [ "D","L","M","M","J","V","S" ],
      monthNames: [ "enero","febrero","marzo","abril","mayo","junio","julio","agosto","septiembre","octubre","noviembre","diciembre" ],
      monthNamesShort: [ "ene","feb","mar","abr","may","jun","jul","ago","sep","oct","nov","dic" ],
      today: 'Hoy',
      clear: 'Borrar'
    }
  }
  eliminarNotificacion(item:any) {
    this.ngxService.start();
    if (item != null) {
      this.service.desactivar(item).subscribe(
        (result: any) => {
          this.ngxService.stop();
          this.entidad = [];
          this.totalRecords = 0;
          this.notifier.notify('success', result.mensaje);
          this.getEntidad();

        },
        (error: any) => {
          this.ngxService.stop();
          this.notifier.notify('error', error.error.mensaje);
        }

      )
    } else {
      this.messageService.add({ severity: 'warning', summary: '¡Advertencia!', detail: 'Seleccione un registro' });
    }

  }
  eliminar(item:any) {
    this.confirmationService.confirm({
      message: '¿Está seguro que desea eliminar la Notificación ?',
      header: 'Confirmar Eliminación',
      icon: 'pi pi-info-circle',
      accept: () => {
        this.eliminarNotificacion(item);
      },
      reject: () => {

      }
    });
  }

  editar(id:any){
    this.router.navigate(['/gestion-interna/notificacion/editar/'+id]);
  }
}
