import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { Mensaje } from 'src/app/config/constantes';
import { CuentacatastralService } from 'src/app/services/cuentacatastral.service';
import { PuntoPagoService } from 'src/app/services/punto-pago.service';
declare var $: any;
declare const google: any;
@Component({
  selector: 'app-punto-pago-form',
  templateUrl: './punto-pago-form.component.html',
  styleUrls: ['./punto-pago-form.component.scss']
})
export class PuntoPagoFormComponent implements OnInit {
  options: any;
  overlays: any;
  municipios: any[] = [];
  titulo: any;
  id: any;
  form: FormGroup;
  activatedView = false;
  activatedEdit = false;
  submit = false;
  tituloBoton = 'Guardar';
  get f() {
    return this.form.controls;
  }
  constructor(private service: PuntoPagoService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService, private notifier: NotifierService,
    private formBuilder: FormBuilder, private route: ActivatedRoute, private serviceCuenta: CuentacatastralService) {
      this.activatedView = this.router.url.includes('/visualizar');
      this.activatedEdit = this.router.url.includes('/editar');
      this.form = this.formBuilder.group(
        {
          nombre: [{value: null, disabled: this.activatedView}, Validators.required],
          direccion: [{value: null, disabled: this.activatedView}, Validators.required],
          telefono: [{value: null, disabled: this.activatedView}, Validators.required],
          latitud: [{value: null, disabled: true}, Validators.required],
          longitud: [{value: null, disabled: true}, Validators.required],
          municipio: [{value: null, disabled: this.activatedView}, Validators.required],
        }
      );

  }



  ngOnInit(): void {
    this.getMunicipios();
    this.titulo = 'Agregar';
    this.id = this.route.snapshot.paramMap.get('id');
    this.ngxService.stop();
    this.options = {
      center: { lat: -25.28646, lng: -57.60933841744386 },
      zoom: 10
    };
    this.overlays = [
      new google.maps.Marker({
        position: { lat: -25.28646, lng: -57.60933841744386 }, title: 'Ubicación',
        icon: {
          url: 'assets/img/icono-mapa.png',
          scaledSize: new google.maps.Size(35, 50),
          origin: new google.maps.Point(0, 0),
          labelOrigin: new google.maps.Point(23, 19)
        },
        draggable: this.activatedView
      }),
    ];
    this.overlays[0].addListener('dragend', (function (notifier) {
      return function () {
      //  notifier.notify('success', Mensaje.UBICACION_ACTUALIZADA);
      };
    })(this.notifier));
    if (this.id != null){
        this.ngxService.stop();
        if (this.activatedView){
          this.titulo = 'Ver';
        }else{
          this.tituloBoton = 'Actualizar';
          this.titulo = 'Editar';
        }
        this.service.getById(parseInt(this.id)).subscribe(
          (response: any) =>  {
            this.ngxService.stop();
            this.form.patchValue({
              nombre: response.nombre,
              direccion: response.direccion,
              telefono: response.telefono,
              latitud: response.latitud,
              longitud: response.longitud,
              municipio: response.municipio.idMunicipio
            });
            this.ubicarMarker();
          },
          (error: any) => {
            this.ngxService.stop();
            this.notifier.notify('error', error.error.mensaje);
          }
      );
    }


  }
  ubicarMarker(){
    this.overlays = [
      new google.maps.Marker({
        position: { lat:  this.form.controls.latitud.value, lng: this.form.controls.longitud.value,   zoom: 10 }, title: 'Ubicación',
        icon: {
          url: 'assets/img/icono-mapa.png',
          scaledSize: new google.maps.Size(35, 50),
          origin: new google.maps.Point(0, 0),
          labelOrigin: new google.maps.Point(23, 19)
        },
        draggable:true

      }),
    ];
  }
  dragend() {
    this.notifier.notify('success', Mensaje.UBICACION_ACTUALIZADA);
  }
  handleMapClick(event:any) {
    this.form.patchValue({
      latitud:this.overlays[0].getPosition().lat(),
      longitud:this.overlays[0].getPosition().lng(),
    });
  }
  getMunicipios() {
    this.ngxService.start();
    this.serviceCuenta.getMunicipios().subscribe(
      (response: any) => {
          this.ngxService.stop();
          this.municipios = response;
      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }
  guardar(){
    this.submit = true;
    if (this.form.invalid) {
      this.notifier.notify('error', Mensaje.VERIFICAR_CAMPOS);
      return;
    } else{
      if(!this.activatedEdit){
        this.ngxService.start();
        let body = {
          nombre: this.form.controls.nombre.value,
          direccion: this.form.controls.direccion.value,
          telefono: this.form.controls.telefono.value,
          latitud: this.form.controls.latitud.value,
          longitud: this.form.controls.longitud.value,
          municipio:{idMunicipio: this.form.controls.municipio.value}
        }
        this.service.save(body).subscribe(
          (response: any) => {
            if(!response.exitoso){
              this.ngxService.stop();
              this.notifier.notify('error', response.mensaje);
             } else {
              this.ngxService.stop();
              this.notifier.notify('success', response.mensaje);
              this.router.navigate(['/gestion-interna/puntos-de-pago']);
             }
          },
          (response: any) => {
            this.ngxService.stop();
            
          }

        )
      } else {
        this.ngxService.start();
        let body = {
          nombre: this.form.controls.nombre.value,
          direccion: this.form.controls.direccion.value,
          telefono: this.form.controls.telefono.value,
          latitud: this.form.controls.latitud.value,
          longitud: this.form.controls.longitud.value,
          municipio:{idMunicipio: this.form.controls.municipio.value}
        }
        this.service.update(this.id, body).subscribe(
          (response: any) => {
             if(!response.exitoso){
              this.ngxService.stop();
              this.notifier.notify('error', response.mensaje);
             } else {
              this.ngxService.stop();
              this.notifier.notify('success', response.mensaje);
              this.router.navigate(['/gestion-interna/puntos-de-pago']);
             }
          },
          (response: any) => {
            this.ngxService.stop();
            
          }

        )
      }
    }

  }

}
