import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NotifierService } from 'angular-notifier';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ConfirmationService, MessageService } from 'primeng/api';
import { BannerService } from 'src/app/services/banner.service';
import { CuentacatastralService } from 'src/app/services/cuentacatastral.service';
import { TokenStorageService } from 'src/app/services/token-storage.service';
import { BannerFormComponent } from '../banner-form/banner-form.component';

@Component({
  selector: 'app-banner-listado',
  templateUrl: './banner-listado.component.html',
  styleUrls: ['./banner-listado.component.scss']
})
export class BannerListadoComponent implements OnInit {

  totalRecords =0;
  entidad:any = [];
  municipios: any = [];
  dataRelacion: any = {};
  page: number = 1;
  size: number = 10;

   constructor(private service: CuentacatastralService, private ngxService: NgxUiLoaderService, private confirmationService: ConfirmationService,
    public router: Router, private messageService: MessageService, private notifier: NotifierService, private authService: TokenStorageService,
    private formBuilder: FormBuilder, private modalService: NgbModal, private serviceBanner: BannerService) {
  }

  ngOnInit(): void {
    this.getMunicipios();
    this.getEntidad();
  }

  getMunicipios() {
    this.ngxService.start();
    this.service.getMunicipios().subscribe(
      (response: any) => {
          this.ngxService.stop();
          this.municipios = response;
      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }

  getEntidad() {
    this.ngxService.start();
    const params = {
      cantidad: 10,
      pagina: this.page,
      sortBy:"idBanner",
      sortOrder: "asc",
      //filtros: JSON.stringify(deleteEmptyData())
    }
    
    this.serviceBanner.get(params).subscribe(
      (response: any) => {
        this.ngxService.stop();
        if(response){

          this.totalRecords = response.total;
          this.entidad = response.lista;
        }else {
          this.totalRecords = 0;
          this.entidad = [];
        }

      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }
  filtrar(){

  }
  limpiar(){

  }

  agregar(){
    const modalRef : any = this.modalService
    .open(BannerFormComponent,{size:'lg',backdrop:'static'});
    modalRef.componentInstance.agregar=true;
    modalRef.result.then((result:any) => {
      this.getEntidad();
    }, (reason: any) => {
      this.getEntidad();
    });
  }
  descargar(id:any){
    this.ngxService.start();
    this.serviceBanner.getById(id).subscribe(
      (response: any) => {
          this.ngxService.stop();
          console.log(response)
          this.decodeBase64(response.imagen, response.titulo);
      },
      (response: any) => {
        this.ngxService.stop();
        
      }

    )
  }
  decodeBase64(file: any, nombre:any ) {
    const blobData = this.convertBase64ToBlobData(file);
    if (window.navigator && window.navigator.msSaveOrOpenBlob) { // IE
      window.navigator.msSaveOrOpenBlob(blobData, nombre);
    } else { // chrome
      const blob = new Blob([blobData], { type: 'application/octet-stream' });
      const url = window.URL.createObjectURL(blob);
      // window.open(url);
      const link = document.createElement('a');
      link.href = url;
      link.download = nombre;
      link.click();
    }
  }
  convertBase64ToBlobData(base64Data: string, contentType: string= 'application/octet-stream', sliceSize= 512) {
    const byteCharacters = atob(base64Data);
    const byteArrays = [];

    for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
      const slice = byteCharacters.slice(offset, offset + sliceSize);

      const byteNumbers = new Array(slice.length);
      for (let i = 0; i < slice.length; i++) {
        byteNumbers[i] = slice.charCodeAt(i);
      }

      const byteArray = new Uint8Array(byteNumbers);

      byteArrays.push(byteArray);
    }

    const blob = new Blob(byteArrays, { type: contentType });
    return blob;
  }
  editar(item: any){
    const modalRef : any = this.modalService
    .open(BannerFormComponent,{size:'lg',backdrop:'static'});
    modalRef.componentInstance.agregar=false;
    modalRef.componentInstance.id=item.idBanner;
    modalRef.componentInstance.banner=item;
    modalRef.result.then((result:any) => {
      this.getEntidad();
    }, (reason: any) => {
      this.getEntidad();
    });
  }
  eliminarBanner(item:any) {
    this.ngxService.start();
    if (item != null) {
      this.serviceBanner.desactivar(item).subscribe(
        (result: any) => {
          this.ngxService.stop();
          this.entidad = [];
          this.totalRecords = 0;
          this.notifier.notify('success', result.mensaje);
          this.getEntidad();

        },
        (error: any) => {
          this.ngxService.stop();
          this.notifier.notify('error', error.error.mensaje);
        }

      )
    } else {
      this.messageService.add({ severity: 'warning', summary: '¡Advertencia!', detail: 'Seleccione un registro' });
    }

  }
  eliminar(item:any) {
    this.confirmationService.confirm({
      message: '¿Está seguro que desea eliminar el banner?',
      header: 'Confirmar Eliminación',
      icon: 'pi pi-info-circle',
      accept: () => {
        this.eliminarBanner(item);
      },
      reject: () => {

      }
    });
  }
  paginate(event: any) {
    this.page = event.page +1;
    this.entidad = [];
    this.getEntidad();
  }

}


